<template>
  <div style="padding:5px;">
     <div v-if="type=='lunbo'"> 
        <div v-for="(item,index) in mydatas.pics" :key="index" style="height: 50px;" class="select-photo-item">
            <el-container>
                <el-aside class="select-photo-photo" style="  height: 48px;" width="49px">
                    <wyu-upload  v-model="mydatas.pics[index].pic" :h="48" :w="48"  :img="mydatas.pics[index].pic"></wyu-upload>
                </el-aside>
                <el-main class="select-photo-main">
                    <div class="select-photo-col">
                        <el-input v-model="mydatas.pics[index].link" size="mini" style="width:80px;float:left; margin-left:10px; margin-right:10px;" placeholder="选择链接"></el-input>
                        <select-link  v-model="mydatas.pics[index].link"></select-link>
                    </div>
                    <i @click="delItem(index)" class="el-icon-circle-close"></i>
                </el-main>
            </el-container>       
        </div> 
    </div>

     <div v-if="type=='default'"> 
        <div v-for="(item,index) in mydatas.pics" :key="index" style="height: 80px;" class="select-photo-item">
            <el-container>
                <el-aside class="select-photo-photo" style="  height: 78px; padding-top:15px; padding-left:10px;" width="69px">
                    <wyu-upload  v-model="mydatas.pics[index].pic" :h="48" :w="48"  :img="mydatas.pics[index].pic"></wyu-upload>
                </el-aside>
                <el-main class="select-photo-main">
                    <div class="select-photo-col">
                       <span class="tit">标题</span> <el-input v-model="mydatas.pics[index].title" size="mini" style="width:80px;float:left; margin-left:10px; margin-right:10px;" placeholder="输入标题"></el-input>
                    </div>
                    <div class="select-photo-col">
                        <span class="tit">链接</span>   <el-input v-model="mydatas.pics[index].link" size="mini" style="width:80px;float:left; margin-left:10px; margin-right:10px;" placeholder="选择链接"></el-input>
                        <select-link  v-model="mydatas.pics[index].link"></select-link>
                    </div>

                    <i @click="delItem(index)" class="el-icon-circle-close"></i>
                </el-main>
            </el-container>       
        </div> 
    </div>
    <div v-if="type=='btn'"> 
        <div v-for="(item,index) in mydatas.pics" :key="index" style="height: 180px;" class="select-photo-item">
            <el-container>
                <el-aside class="select-photo-photo" style="  height: 178px; padding-top:15px; padding-left:10px;" width="69px">
                    <wyu-upload  v-model="mydatas.pics[index].pic" :h="48" :w="48"  :img="mydatas.pics[index].pic"></wyu-upload>
                </el-aside>
                <el-main class="select-photo-main">
                    <div class="select-photo-col">
                       <span class="tit">标题</span> <el-input v-model="mydatas.pics[index].title" size="mini" style="width:80px;float:left; margin-left:10px; margin-right:10px;" placeholder="输入标题"></el-input>
                    </div>
                    <div class="select-photo-col">
                        <span class="tit">链接</span>   <el-input v-model="mydatas.pics[index].link" size="mini" style="width:80px;float:left; margin-left:10px; margin-right:10px;" placeholder="选择链接"></el-input>
                        <select-link  v-model="mydatas.pics[index].link"></select-link>
                    </div>
                     <div class="select-photo-col">
                         <span class="tit">颜色</span>
                        <el-color-picker v-model="mydatas.pics[index].color" style="float:left; margin-left:10px; margin-right:10px;" size="mini"></el-color-picker>
                        <span class="tit">背景</span>
                        <el-color-picker v-model="mydatas.pics[index].bg" style="float:left; margin-left:10px; margin-right:10px;" size="mini"></el-color-picker>
                     </div>
                     <div class="select-photo-col">
                        <span class="tit">投影</span>
                        <el-switch
                            style="float:left; margin-left:10px; margin-right:10px;"
                            v-model="mydatas.pics[index].shadow"
                            :active-value="1"
                            :inactive-value="0"
                         >
                         </el-switch>

                          <span v-if="mydatas.pics[index].shadow == 1" class="tit">投影颜色</span>
                        <el-color-picker v-if="mydatas.pics[index].shadow == 1"  v-model="mydatas.pics[index].shadowColor" style="float:left; margin-left:10px; margin-right:10px;" size="mini"></el-color-picker>

                     </div>
                     <div  class="select-photo-col">
                            <span class="tit">圆角</span>
                            <div style="float:left;margin-left:20px; margin-right:10px; width:220px;">
                            <el-slider  :min="0" :max="mydatas.lineH"  v-model="mydatas.pics[index].radius"></el-slider>
                            </div>
                        
                     </div>





                    <i @click="delItem(index)" class="el-icon-circle-close"></i>
                </el-main>
            </el-container>       
        </div> 
    </div>

        <div v-if="type=='gongge'"> 
                <div v-if="mydatas.type == 0">  
                    <div v-for="(item,index) in mydatas.pics" :key="index" style="height: 50px;" class="select-photo-item">
                    <el-container>
                        <el-aside class="select-photo-photo" style="  height: 48px;" width="49px">
                            <wyu-upload  v-model="mydatas.pics[index].pic" :h="48" :w="48"  :img="mydatas.pics[index].pic"></wyu-upload>
                        </el-aside>
                        <el-main class="select-photo-main">
                            <div class="select-photo-col">
                                <el-input v-model="mydatas.pics[index].link" size="mini" style="width:80px;float:left; margin-left:10px; margin-right:10px;" placeholder="选择链接"></el-input>
                                <select-link  v-model="mydatas.pics[index].link"></select-link>
                            </div>
                            <i @click="delItem(index)" class="el-icon-circle-close"></i>
                        </el-main>
                    </el-container>       
                </div>
                </div>
            <div v-if="mydatas.type > 0">   

                <div v-for="(item,index) in mydatas.pics" :key="index" style="height: 280px;" class="select-photo-item">
                    <el-container>
                        <el-aside class="select-photo-photo" style="  height: 278px;" width="69px">
                            <wyu-upload style="float:left;margin-top:85px;margin-left:10px;"  v-model="mydatas.pics[index].pic" :h="48" :w="48"  :img="mydatas.pics[index].pic"></wyu-upload>
                        </el-aside>
                        <el-main class="select-photo-main">
                            
                            <div class="select-photo-col">
                                <span class="tit">标题</span>
                                <el-input v-model="mydatas.pics[index].title" size="mini" style="width:80px;float:left; margin-left:10px; margin-right:10px;" placeholder="输入标题"></el-input>
                                <span class="tit">副标题</span>
                                <el-input v-model="mydatas.pics[index].desc" size="mini" style="width:80px;float:left; margin-left:10px; margin-right:10px;" placeholder="输入副标题"></el-input>
                            </div>   
                            <div class="select-photo-col">
                                <span class="tit">链接</span> <el-input v-model="mydatas.pics[index].link" size="mini" style="width:80px;float:left; margin-left:10px; margin-right:10px;" placeholder="选择链接"></el-input>
                                <select-link  v-model="mydatas.pics[index].link"></select-link>
                            </div>
                            <div class="select-photo-col">
                                <span class="tit">标题字号</span>
                                <el-input v-model="mydatas.pics[index].titleSize" size="mini" style="width:50px;float:left; margin-left:10px; margin-right:10px;"></el-input>
                                <span class="tit">副标题字号</span>
                                <el-input v-model="mydatas.pics[index].descSize" size="mini" style="width:50px;float:left; margin-left:10px; margin-right:10px;"></el-input>
                            </div> 
                            <div class="select-photo-col">
                                <span class="tit">标题颜色</span>
                                <el-color-picker v-model="mydatas.pics[index].titleColor" style="float:left; margin-left:10px; margin-right:10px;" size="mini"></el-color-picker>

                                <span class="tit">副标题颜色</span>
                                <el-color-picker v-model="mydatas.pics[index].descColor" style="float:left; margin-left:10px; margin-right:10px;" size="mini"></el-color-picker>
                            </div>   
                            <div class="select-photo-col">
                                <span class="tit">标题是否加粗</span>
                                <el-switch
                                    v-model="mydatas.pics[index].isB"
                                    :active-value="1"
                                    :inactive-value="0"
                                >
                                </el-switch>
                            </div>
                            <div class="select-photo-col">
                                <span class="tit">背景类型</span>
                                <el-radio-group style="float:left;"  v-model="mydatas.pics[index].bgType" >
                                    <el-radio  style="margin-left:20px; margin-right:10px;"  :label="0">纯色</el-radio>
                                    <el-radio  style="margin-left:0px; margin-right:10px;"  :label="1">上下渐变</el-radio>
                                    <el-radio  style="margin-left:0px; margin-right:10px;"  :label="2">左右渐变</el-radio>
                                </el-radio-group>
                            </div>    
                            <div class="select-photo-col">
                                <span class="tit">背景颜色</span>
                                <el-color-picker v-model="mydatas.pics[index].bg" style="float:left; margin-left:10px; margin-right:10px;" size="mini"></el-color-picker>

                                <span v-if="mydatas.pics[index].bgType > 0" class="tit">渐变色</span>
                                <el-color-picker  v-if="mydatas.pics[index].bgType > 0" v-model="mydatas.pics[index].bg2" style="float:left; margin-left:10px; margin-right:10px;" size="mini"></el-color-picker>
                            </div>   
                    
                
                            <i @click="delItem(index)" class="el-icon-circle-close"></i>
                        </el-main>
                    </el-container>       
                </div> 
            </div>    
        </div>

    <el-button @click="addItem" v-if="mydatas.pics.length < mydatas.maxNum" type="text">添加一条</el-button>    
  </div>
</template>

<script>
import SelectLink from  './SelectLink';
import WyuUpload from "@/components/Photos";


export default {
  name: 'selectPhoto',
  props: ['datas','type'],
  components:{
      SelectLink,
      WyuUpload
  },
  data(){
      return {
          mydatas:[],
      };
  },
  watch:{
      datas:{
           handler(newName, oldName) {
                if(this.datas != this.mydatas){
                    this.mydatas = this.datas;
                }
            },
            immediate: true,
            deep: true
      },
      mydatas:{
           handler(newName, oldName) {
                if(this.datas != this.mydatas){
                    this.$emit('input',this.mydatas);
                }
            },
            immediate: true,
            deep: true
      }
  },
  created() {
      this.mydatas = this.datas;
  },
  methods: {
      delItem(index){
          this.mydatas.pics.splice(index,1);
      },
      addItem(){
          if(this.type == 'lunbo'){
              if(this.mydatas.pics.length < this.mydatas.maxNum){
                this.mydatas.pics.push({
                    pic:'',link:''
                });    
              }
          }

          if(this.type == 'default'){
              if(this.mydatas.pics.length < this.mydatas.maxNum){
                this.mydatas.pics.push({
                    pic:'',link:'',title:'自定义',
                });    
              }
          }

          if(this.type == 'gongge'){
                if(this.mydatas.pics.length < this.mydatas.maxNum){
                    this.mydatas.pics.push(            
                         {pic:'',link:'',title:'标题',desc:'这个是副标题',titleSize:16,isB:1,descSize:12,titleColor:'#333333',descColor:'#999999',bgType:0,bg:'#f2f2f2',bg2:''}
                    );    
                }
          }

          if(this.type == 'btn'){
               if(this.mydatas.pics.length < this.mydatas.maxNum){
                    this.mydatas.pics.push(       
                        {pic:'',title:'自定义',link:'',bg:'#FF8C00',  radius:5,color:'#ffffff',shadow:1,shadowColor:'#DEDEDE'}
                    );    
                }
          }


      },
  },
}
</script>
<style scoped>
    .select-photo-item{
        width: 100%;
        border: 1px solid #DCDFE6;
        margin-top: 5px;
        position: relative;
        background: #F2F6FC;
    }
    .select-photo-photo{
        border-right:  1px solid #DCDFE6;
    }

 
    .select-photo-main i{
        position:absolute;
        right: -5px;
        top: -5px;
        color: #999999;
        font-size: 16px;
        cursor: pointer;
    }
    .select-photo-col{
        height: 30px;
        line-height: 30px;
        margin-top:5px;
        font-size: 13px;
    }

    .select-photo-col .tit{ float: left; margin-left: 5px;}
</style>
