<template>
  <span class="selectIcon">
   <a @click="selectIconlist" href="javascript:void(0);">选择图标</a>

    <el-dialog
        title="选择图标"
        :visible.sync="dialogVisible"
        :modal-append-to-body="false"
        width="800px"
       >

       <el-tabs :tab-position="tabPosition" style="min-height:500px;">
            <el-tab-pane label="图标库">
                <div class="icon-main-body" v-loading="loading">
                    <el-form style="padding-bottom:10px; width:200px;">
                        <el-input placeholder="请输入要搜索的内容" v-model="icons.title">
                                <el-button @click="getSearchIcons" slot="append" icon="el-icon-search"></el-button>
                        </el-input>
                    </el-form>
                    <ul class="icon-select-list">
                    <li v-for="(item,index) in icons.list" :key="index" @click="selectIconRows(item)" class="icon-select-item">
                            <img :src="item.checked" />
                            <img :src="item.unchecked" />
                            <p>{{item.title}}</p>
                    </li>
                    </ul>
                    <el-pagination
                        class="pages"
                        small
                        @current-change="changeIcons"
                        layout="prev, pager, next"
                        :page-size="pagesize"
                        :total="icons.total">
                    </el-pagination>
                </div>
            </el-tab-pane>
            <el-tab-pane label="我的图标">
                <div class="icon-main-body" v-loading="loading2">
                    <el-form style="padding-bottom:10px; width:200px;">
                        <el-input placeholder="请输入要搜索的内容" v-model="myIcons.title">
                                <el-button @click="getSearchMyIcons" slot="append" icon="el-icon-search"></el-button>
                        </el-input>
                    </el-form>
                    <ul class="icon-select-list">
                    <li v-for="(item,index) in myIcons.list" :key="index" @click="selectIconRows(item)" class="icon-select-item">
                            <img :src="item.checked" />
                            <img :src="item.unchecked" />
                            <p>{{item.title}}</p>
                    </li>
                    </ul>
                    <el-pagination
                        class="pages"
                        small
                        @current-change="changeMyIcons"
                        layout="prev, pager, next"
                        :page-size="pagesize"
                        :total="myIcons.total">
                    </el-pagination>
                </div>
            </el-tab-pane>
            <el-tab-pane label="上传图标">
                <el-form  size="mini" label-width="100px" :model="uploadIcon">
                  <el-form-item label="图标标题">
                        <el-input v-model="uploadIcon.name" style="width:100px;" placeholder="输入图标标题方便查找"></el-input>
                  </el-form-item>
                  <el-form-item label="选中图标">
                           <wyu-upload  v-model="uploadIcon.checked" :w="50" :h="50" :img="uploadIcon.checked"></wyu-upload>
                          <p>建议尺寸为64像素透明背景的PNG图标，阿里巴巴图标<a target="_blank" href="https://www.iconfont.cn">下载</a></p>
                  </el-form-item>
                  <el-form-item label="未选中图标">
                         <wyu-upload  v-model="uploadIcon.unchecked" :w="50" :h="50"  :img="uploadIcon.unchecked"></wyu-upload>
                      <p>建议尺寸为64像素透明背景的PNG图标，阿里巴巴图标<a target="_blank" href="https://www.iconfont.cn">下载</a></p>
                  </el-form-item>
                  <el-form-item>
                        <el-button @click="saveIcon" :loading="loading3">保存并选取</el-button>
                  </el-form-item>

                </el-form>

            </el-tab-pane>
       </el-tabs>



    </el-dialog>

  </span>

</template>

<script>
import WyuUpload from "@/components/Photos";
import * as api from '@/api.js';

export default {
  name: 'selectIcon',
  props:['itemindex'],
  components:{
      WyuUpload
  },
  data(){
      return {
            loading:false,
            loading2:false,
            loading3:false,
            pagesize:15,
            dialogVisible:false,
            tabPosition:'left',

            uploadIcon:{
                name:'',
                checked:'',
                unchecked:'',
            },

            icons:{
                total:0,
                title:'',
                page:1,
                list:[],//数据
            },
            myIcons:{
                total:0,
                title:'',
                page:1,
                list:[],//数据
            },
      };
  },
  methods:{
      selectIconlist(){
         this.dialogVisible = true;
         if(this.icons.list == ''){
            this.getIcons();
         }
         if(this.myIcons.list == ''){
            this.getMyIcons();
         }
      },
      selectIconRows(item){
          let back = {
              itemindex:this.itemindex,
              checked:item.checked,
              unchecked:item.unchecked
          };
          this.$emit('selectIcon',back);
          this.dialogVisible = false;
      },
      getSearchIcons(){
          this.icons.page = 1;
          this.getIcons();
      },
      changeIcons(val){
          this.icons.page = val;
          this.getIcons();
      },
      getIcons(){
           this.loading = true;
          api.post('shop/admin.app.footericon/index',{
                 appToken:this.$store.getters.appToken,
                 title:this.icons.title,
                 limit:this.pagesize,
                 page:this.icons.page,
              }).then(res=>{
                 this.icons.total = res.count;
                 this.icons.list = res.list;
                 this.loading = false;
              }).catch((error) => {
                 this.loading = false;
              });
      },
      saveIcon(){
          if(this.uploadIcon.name == ''){
              this.$message({
                    message: '图标名称不能为空',
                    type: 'warning'
                });
          }else if(this.uploadIcon.checked ==''){
                 this.$message({
                    message: '请上传图标',
                    type: 'warning'
                });
          }else if(this.uploadIcon.unchecked == ''){
                this.$message({
                    message: '请上传图标',
                    type: 'warning'
                });
          }else{
                  this.loading3 = true;
                    api.post('shop/admin.app.footericon/create',{
                            appToken:this.$store.getters.appToken,
                            title:this.uploadIcon.name,
                            unchecked:this.uploadIcon.unchecked,
                            checked:this.uploadIcon.checked,
                    }).then(res=>{
                            this.selectIconRows(this.uploadIcon);
                            this.getMyIcons();
                            this.loading3 = false;
                    }).catch((error) => {
                            this.loading3 = false;
                    });
          }
      },

       getSearchMyIcons(){
          this.myIcons.page = 1;
          this.getMyIcons();
      },
      changeMyIcons(val){
          this.myIcons.page = val;
          this.getMyIcons();
      },
      getMyIcons(){
          this.loading2 = true;
          api.post('shop/admin.app.footericon/myIndex',{
                 appToken:this.$store.getters.appToken,
                 limit:this.pagesize,
                 page:this.myIcons.page,
              }).then(res=>{
                 this.myIcons.total = res.count;
                 this.myIcons.list = res.list;
                 this.loading2 = false;
              }).catch((error) => {
                 this.loading2 = false;
              });
      },
  },
}
</script>

<style scoped>
.selectIcon a{color:#999999}
.icon-main-body{
    height: auto;
    overflow: hidden;
}
.icon-select-item{
    float: left;
    margin-right: 10px;
    margin-top:10px;
    padding: 5px;
    border: 1px dashed #f2f2f2;
    font-size: 13px;
    text-align: center;
    cursor: pointer;
}
.icon-select-item img{
    width: 30px;
    margin-right: 5px;
}
.icon-select-item:hover{
    border-color: #409EFF;
    color:#409EFF;
}
.icon-select-list{
    width: 100%;
    height: auto;
    overflow: hidden;
}
.pages{
    margin-top: 10px;
}
</style>
