<template>
  <div>
      <div v-if="type == 'show'"  :style="getStyle()">        
           <el-row :gutter="mydatas.mid">
              <el-col v-for="(item,index) in mydatas.pics" :key="index" :span="8">
                  <div class="plugin-gongge-item" :style="{height:mydatas.height+'px',marginTop:index>=3 ? mydatas.mid+'px' : '0px'}">
                        <img :style="{height:mydatas.height+'px'}" :src="item.pic ? item.pic:'/miniapp/shop/img/img.png'"/>
                  </div>
              </el-col>
            </el-row>
      </div>
      <div v-if="type  == 'edit'" >
             <el-tabs v-model="activeName">
                <el-tab-pane label="九宫格设置" name="first">
                   
                   <select-photo :type="'lunbo'" :datas="mydatas" v-model="mydatas"></select-photo>
                    <el-form size="mini" label-width="80px" style="margin-top:10;">
                        <el-form-item label="图片高度">
                             <el-slider  :min="50" :max="200"  v-model="mydatas.height"></el-slider>
                        </el-form-item>
                        <el-form-item label="图片间距">
                             <el-slider  :min="0" :max="20"  v-model="mydatas.mid"></el-slider>
                        </el-form-item>
                    </el-form>
                </el-tab-pane>
                <el-tab-pane label="基本设置" name="second">                      
                     <edit-com :datas="mydatas" v-model="mydatas"></edit-com>
                </el-tab-pane>
               
            </el-tabs>
         
      </div>
  </div>
</template>

<script>
import editCom from '../EditCom.vue';
import SelectLink from  '../SelectLink';
import selectPhoto from '../SelectPhoto';
export default {
  name: 'pluginGonggejiu',
  props: ['type', 'datas'],
  components:{
      editCom,
      SelectLink,
      selectPhoto
  },
  data(){
      return {
          activeName:'first',
          mydatas:[],
      };
  },
  watch:{
      datas:{
           handler(newName, oldName) {
                if(this.datas != this.mydatas){
                    this.mydatas = this.datas;
                }
            },
            immediate: true,
            deep: true
      },
      mydatas:{
           handler(newName, oldName) {
                if(this.datas != this.mydatas){
                    this.$emit('input',this.mydatas);
                }
            },
            immediate: true,
            deep: true
      }
  },
  methods: {
      getStyle(){
           let style = new Object;
           
          
           return style;
      },
  },
}
</script>
<style scoped>
    .plugin-gongge-item{
        width: 100%;
    }
    .plugin-gongge-item img{
        width: 100%;
    }
</style>