<template>
  <div>
      <div v-if="type == 'show'"  :style="getStyle()">
         
            <div v-if="mydatas.showType == 2 && showDatas!=''">
                    <el-row :gutter="15">
                      <el-col v-for="(item,index) in showDatas" :key="index" :span="12">
                          <div class="ms-item-2">
                              <img :src="item.photo?item.photo:'/miniapp/shop/img/img.png'" />
                          </div>
                          <div class="price">
                            <span class="pricefh" :style="{color:mydatas.mainColor}" >￥</span><span :style="{color:mydatas.mainColor}"  class="nprice">{{item.bargain_price}}</span> 
                              <span class="tit" :style="{color:mydatas.fzColor}">原价:</span>  <span :style="{color:mydatas.fzColor}" class="uprice">￥{{item.price}}</span>
                          </div>
                        
                      </el-col>
                    </el-row>
            </div>
            <div v-if="mydatas.showType == 3 && showDatas!=''">
                 <el-row :gutter="9">
                         <el-col v-for="(item,index) in showDatas" :key="index" :span="8">
                            <div class="ms-item-3">
                              <img :src="item.photo?item.photo:'/miniapp/shop/img/img.png'"/>
                          </div>
                        <div class="price2">
                         <span class="pricefh" :style="{color:mydatas.mainColor}" >￥</span><span :style="{color:mydatas.mainColor}"  class="nprice">{{item.bargain_price}}</span> 
                           <span :style="{color:mydatas.fzColor}" class="uprice">￥{{item.price}}</span>
                          </div>
                       
                        </el-col>
                 </el-row>
            </div>

             <div v-if="showDatas == ''" class="plugin-loadding">{{loading == true ? '数据正在加载中' :'您还未添加内容'}}</div>
      </div>
      <div v-if="type  == 'edit'" >
          <el-form style="padding-bottom:0px;"  size="small" label-width="80px">
                <el-form-item label="样式">
                        <el-radio v-model="mydatas.showType" :label="2">样式一</el-radio>
                        <el-radio v-model="mydatas.showType" :label="3">样式二</el-radio>
                </el-form-item>
                <el-form-item   label="主要颜色">  
                    <el-color-picker  v-model="mydatas.mainColor"  size="mini"></el-color-picker>
                </el-form-item>
                <el-form-item   label="次要颜色">  
                    <el-color-picker  v-model="mydatas.fzColor"  size="mini"></el-color-picker>
                </el-form-item>   
          
          </el-form>

         <edit-com :datas="mydatas" v-model="mydatas"></edit-com>
      </div>
  </div>
</template>
<style scoped>
  .ms-item-2{
      width: 100%;
      height: 165px;
      position: relative;
  }
  .ms-item-2 img,.ms-item-3 img{
      width: 100%;
      height: 100%;
  }
 
  .ms-item-3{
      width: 100%;
      height: 109px;
      position: relative;
  }
  .ms-item-2 span, .ms-item-3 span{
      position: absolute;
      left: 0px;
      top:0px;
      padding: 3px 8px;
      border-bottom-right-radius:10px;
      color:#fff; 
      font-size: 12px;
  }

  .ms-tit{
        font-size:16px;
        font-weight:600;
        color:rgba(51,51,51,1);
        line-height:22px;
        margin-top: 12px;
        height:44px;
        overflow: hidden;
  }
  .price .pricefh{
      font-size: 14px;
  }
  .price .nprice{
      font-size:18px;
      font-weight: 600;
      margin-left: 5px;
  }
  .price{
      margin-top: 8px;
  }
  .price .uprice{
      color:#999999;
      font-size: 14px;
      text-decoration:line-through;
  }
  .price .tit{
      color:#999999;
      font-size: 14px;
      margin-left: 10px;
  }

   .price2 .pricefh{
      font-size: 13px;
  }
  .price2 .nprice{
      font-size:14px;
      font-weight: 600;
  }
  .price2{
      margin-top: 8px;
  }
  .price2 .uprice{
      color:#999999;
      font-size: 13px;
      text-decoration:line-through;
      margin-left: 10px;
  }
  .price2 .tit{
      color:#999999;
      font-size: 13px;
  }
</style>
<script>
import * as api from "@/api";
import editCom from '../EditCom.vue';
export default {
  name: 'pluginKanjia',
  props: ['type','theme', 'datas'],
  components:{
      editCom
  },
  data(){
      return {
          mydatas:[],
          showDatas:[],
          showType:0,
          myThemes:[],
          loading:false,
      };
  },
  watch:{
      theme:{
          handler(newName, oldName) {
                if(this.theme != this.myThemes){
                    this.myThemes = this.theme;
                }
            },
            immediate: true,
            deep: true
      },
      datas:{
           handler(newName, oldName) {
                if(this.datas != this.mydatas){
                    this.mydatas = this.datas;
                }
            },
            immediate: true,
            deep: true
      },
      mydatas:{
           handler(newName, oldName) {
                this.getCalldata();
                if(this.datas != this.mydatas){
                    this.$emit('input',this.mydatas);
                }
            },
            immediate: true,
            deep: true
      }
  },
 
  methods: {
      getCalldata(){
          if(this.type != 'show' ) return;
          if(this.mydatas.showType != this.showType){
                this.showType = this.mydatas.showType;
                if(this.loading == false){
                         this.loading = true;
                        api.post("/shop/admin.marketing.bargain/getCalldata", {
                            appToken: this.$store.getters.appToken,
                            showType:this.showType,
                        }).then(res => {
                            if(res.goods){
                                this.showDatas = res.goods;
                            }
                            this.loading = false;
                        }).catch((error) => {
                            this.loading = false;
                        });
                }
          }
      },
     
      getStyle(){
           let style = new Object;

           return style;
      },
     
  },
}
</script>
