<template>
  <div>
      <div v-if="type == 'show'"  :style="getStyle()">        

           <ul>
               <li v-for="(item,index) in mydatas.pics" :key="index" class="plugin-icon" :style="getStyle2()">
                   <img :src="item.pic ? item.pic : '/miniapp/shop/img/img.png'"  :style="getStyleImage(item)"/>
                   <p :style="getStyleByItem(item)">{{item.title}}</p>
               </li>
           </ul>

      </div>
      <div v-if="type  == 'edit'" >
             <el-tabs v-model="activeName">
                <el-tab-pane label="图文设置" name="first">

                    <select-photo :type="'default'" :datas="mydatas" v-model="mydatas"></select-photo>

                    <el-form size="mini" label-width="110px" style="margin-top:10;padding-bottom:10px;">
                        <el-form-item  label="ICON高度">
                             <el-slider  :min="40" :max="80"  v-model="mydatas.height"></el-slider>
                        </el-form-item>
                        <el-form-item  label="ICON宽度">
                             <el-slider  :min="40" :max="80"  v-model="mydatas.width"></el-slider>
                        </el-form-item>

                        <el-form-item  label="每行显示">
                             <el-slider  :min="2" :max="5"  v-model="mydatas.num"></el-slider>
                        </el-form-item>

                        <el-form-item label="标题字号">
                             <el-slider  :min="12" :max="50"  v-model="mydatas.titleSize"></el-slider>
                        </el-form-item>
                         <el-form-item label="标题加粗">    
                                <el-radio style="margin-left:0px; margin-right:10px;" v-model="mydatas.isB" :label="0">默认</el-radio>
                                <el-radio style="margin-left:0px; margin-right:10px;"  v-model="mydatas.isB" :label="1">加粗</el-radio>
                        </el-form-item> 
                          
                        <el-form-item   label="文字颜色">  
                            <el-color-picker  v-model="mydatas.titleColor"  size="mini"></el-color-picker>
                        </el-form-item>
                   
                         <el-form-item label="文字容器高度">
                             <el-slider   v-model="mydatas.titleH"></el-slider>
                        </el-form-item>
                    </el-form>
                </el-tab-pane>
                <el-tab-pane label="基本设置" name="second">                      
                     <edit-com :datas="mydatas" v-model="mydatas"></edit-com>
                </el-tab-pane>
               
            </el-tabs>
         
      </div>
  </div>
</template>

<script>
import editCom from '../EditCom.vue';
import selectPhoto from '../SelectPhoto';
export default {
  name: 'pluginIconlist',
  props: ['type', 'datas'],
  components:{
      editCom,
      selectPhoto
  },
  data(){
      return {
          activeName:'first',
          mydatas:[],
      };
  },
  watch:{
      datas:{
           handler(newName, oldName) {
                if(this.datas != this.mydatas){
                    this.mydatas = this.datas;
                }
            },
            immediate: true,
            deep: true
      },
      mydatas:{
           handler(newName, oldName) {
                if(this.datas != this.mydatas){
                    this.$emit('input',this.mydatas);
                }
            },
            immediate: true,
            deep: true
      }
  },
  methods: {
      getStyle(){
           let style = new Object;
           
          
           return style;
      },
      getStyle2(){
          let style = new Object;
          
          style.width = 100/this.mydatas.num  + '%';

           return style;
      },
      getStyleImage(item){
          let style = new Object;
          style.height = this.mydatas.height+'px';
          style.width  = this.mydatas.width+'px';  
          return style;  
      },
      getStyleByItem(item){
          let style = new Object;
          style.height = this.mydatas.titleH+'px';
      
          style.fontWeight = this.mydatas.isB ? 'bold' : '100';
          style.fontSize = this.mydatas.titleSize + 'px';
          style.color = this.mydatas.titleColor; 
          style.lineHeight =   this.mydatas.titleH+'px';
          return style;  
      },
   
  },
}
</script>
<style scoped>
  .plugin-icon{
      float:left;
      text-align:center;
         padding-top:8px;  
  }

  .plugin-icon  img{
      display:block;
      margin:0 auto;
  }
</style>