<template>
  <div>
      <div v-if="type == 'show'"  :style="getStyle()">
            {{mydatas.txt}}
      </div>
      <div v-if="type  == 'edit'" >
             <el-tabs v-model="activeName">
                <el-tab-pane label="内容设置" name="first">
                    <el-form style="padding:10px;" size="mini" label-width="80px">
                        <el-form-item label="输入内容">
                             <el-input
                                    type="textarea"
                                    :rows="3"
                                    placeholder="请输入内容"
                                    v-model="mydatas.txt">
                            </el-input>
                        </el-form-item>
                        <el-form-item label="字体行高">    
                             <el-slider  :min="12" :max="60"  v-model="mydatas.lineH"></el-slider>
                        </el-form-item>     
                        <el-form-item label="对齐方式">    
                            <el-select v-model="mydatas.type" placeholder="">
                                 <el-option label="左对齐" :value="0"></el-option>
                                 <el-option label="居中对齐" :value="1"></el-option>
                                 <el-option label="右对齐" :value="2"></el-option>
                                 <el-option label="首行缩进" :value="3"></el-option>
                            </el-select>
                        </el-form-item> 
                        <el-form-item label="字体大小">    
                             <el-slider :min="12" :max="60" v-model="mydatas.fontsize"></el-slider>
                        </el-form-item>   
                        <el-form-item label="是否加粗">    
                                <el-radio v-model="mydatas.isB" :label="0">默认</el-radio>
                                <el-radio v-model="mydatas.isB" :label="1">加粗</el-radio>
                        </el-form-item>   
                        <el-form-item   label="文字颜色">  
                            <el-color-picker  v-model="mydatas.color"  size="mini"></el-color-picker>
                        </el-form-item>

                        <el-form-item label="链接地址">    
                               <el-input placeholder="选择链接" v-model="mydatas.link"  class="input-select-link"></el-input>
                               <select-link v-model="mydatas.link"></select-link>
                        </el-form-item>          


                    </el-form>  
                </el-tab-pane>
                <el-tab-pane label="基本设置" name="second">                      
                     <edit-com :datas="mydatas" v-model="mydatas"></edit-com>
                </el-tab-pane>
               
            </el-tabs>
         
      </div>
  </div>
</template>

<script>
import editCom from '../EditCom.vue';
import SelectLink from  '../SelectLink';

export default {
  name: 'pluginTxt',
  props: ['type', 'datas'],
  components:{
      editCom,
      SelectLink
  },
  data(){
      return {
          activeName:'first',
          mydatas:[],
      };
  },
  watch:{
      datas:{
           handler(newName, oldName) {
                if(this.datas != this.mydatas){
                    this.mydatas = this.datas;
                }
            },
            immediate: true,
            deep: true
      },
      mydatas:{
           handler(newName, oldName) {
                if(this.datas != this.mydatas){
                    this.$emit('input',this.mydatas);
                }
            },
            immediate: true,
            deep: true
      }
  },
  methods: {
      getStyle(){
           let style = new Object;
           
           style.lineHeight = this.mydatas.lineH + 'px';
           style.color = this.mydatas.color;
           style.fontSize = this.mydatas.fontsize + 'px';
           style.wordWrap = 'break-word';
            if(this.mydatas.isB == 1){
                style.fontWeight = 'bold';
            } 

           if(this.mydatas.type == 0){
               style.textAlign = 'left';
           }else if(this.mydatas.type == 1){
                style.textAlign = 'center';
           }else  if(this.mydatas.type == 2){
               style.textAlign = 'right';
           }else{
               style.textIndent = (this.mydatas.fontsize * 2) +'px';
           }

           return style;
      },
  },
}
</script>
