<template>
  <div v-if="type == 'edit'" class="icon-edit">
       <div v-for="(item,index) in myIcons.list" :key="index" class="icon-item">
            <el-form size="mini" style="padding-bottom:0px;" label-width="80px">
                <el-form-item label="菜单名称">
                  <el-input  size="mini" style="width:120px;" v-model="myIcons.list[index].title" placeholder="请输入菜单名称"></el-input>
                </el-form-item>
                <el-form-item label="链接地址">
                    <el-input placeholder="选择链接" v-model="myIcons.list[index].link"  class="input-with-select"></el-input>
                    <select-link v-model="myIcons.list[index].link"></select-link>
                </el-form-item>
                <el-form-item label="图标样式">
                      <img  class="img" v-if="myIcons.list[index].icon != ''"  :src="myIcons.list[index].icon"/> 
                      <img class="img"  v-if="myIcons.list[index].iconselect != ''"  :src="myIcons.list[index].iconselect"/>
                      <select-icon :itemindex="index" @selectIcon="selectIcon"></select-icon>
                </el-form-item>
                <el-form-item label="显示文字">
                   <el-switch
                      active-color="#13ce66"
                      inactive-color="#ff4949"
                      v-model="myIcons.list[index].is_title"
                      :active-value="1"
                      :inactive-value="0">
                    </el-switch>
                </el-form-item>
            </el-form>   
            <a href="javascript:void(0);" @click="delIcon(index)"  class="icon-del el-icon-error"></a>
       </div>
       <el-button v-if="myIcons.list.length < 5" @click="addIcon"  type="text">+新增菜单</el-button>
  </div>
  <div v-else class="icon-show">
      <ul>
        <li v-bind:style="'width:'+(100/myIcons.list.length)+'%'" v-for="(footer,a) in myIcons.list" v-bind:key="a">
          <img v-if="footer.is_title == 1" width="22" height="22" v-bind:src="a == 0? (footer.iconselect ? footer.iconselect:'/miniapp/shop/img/img.png') : (footer.icon ? footer.icon:'/miniapp/shop/img/img.png')" />
          <p  v-if="footer.is_title == 1" v-bind:style="'color:'+ (a==0 ? myIcons.selectColor:'#999999')" >{{footer.title}}</p>
          <img  v-if="footer.is_title != 1" width="36" height="36"  v-bind:src="a == 0? footer.iconselect : footer.icon"  />
        </li>
      </ul>
  </div>
</template>

<script>
import SelectLink from  './SelectLink';
import SelectIcon from './SelectIcon';

export default {
  name: 'icon',
  props:['type','icons'],
  components:{
    SelectLink,
    SelectIcon
  },
  data(){
      return {
          myIcons: null,
      };
  },
  watch:{
     icons:{
          handler(newName, oldName) {
              if(this.myIcons != this.icons){
                this.myIcons = this.icons;
              }
          },
          immediate: true,
          deep: true
      },
      myIcons:{
         handler(newName, oldName) {
              if(this.myIcons != this.icons){
                  this.$emit("input",this.myIcons);
              }
          },
          immediate: true,
          deep: true
      },
  },
  methods:{
    delIcon(index){
        this.myIcons.list.splice(index,1);
    },
    selectIcon(res){
        this.myIcons.list[res.itemindex].icon = res.unchecked;
         this.myIcons.list[res.itemindex].iconselect = res.checked;
    },
    addIcon(){
      this.myIcons.list.push({
          is_title:1,//0代表不显示文字
          title:'自定义',//
          icon:'',
          iconselect:'',
          link:'',
      });
    },
  },
}
</script>

<style scoped>
  .icon-item{
    position: relative;
    margin-bottom: 10px;
    padding: 10px;
    height: auto;
    width: 100%;
    border:1px solid #DCDFE6;
  }
  .icon-item .img{
    width: 30px;
    margin-right: 10px;
  }
  .icon-item a{
    color:#999999;
  }
  .icon-del{
    position:absolute;
    right: -5px;
    top: -5px;
    display: none;
    font-size: 20px;
    color:rgba(0,0,0,.2);
  }
    .icon-item:hover .icon-del{
      display: flex;
    }
    .icon-show li{
      float: left;
          text-align: center;
    margin-top: 5px;
    height: 45px;
    }

    .icon-show li p{
          font-size: 12px;
    line-height: 20px;
    }

    .input-with-select{
      width: 120px;
      float: left;
    }
</style>
