<template>
  <div>
      <div v-if="type == 'show'"  :style="getStyle()">
           <el-row :gutter="mydatas.pics.length > 1 ? mydatas.mid : 0">
               <el-col v-for="(item,index) in mydatas.pics" :key="index" :span="mydatas.pics.length > 1 ? 12 : 24">
                   <div class="plugin-btn" :style="getStyleItem(item)">
                       <img v-if="item.pic != ''" :style="{height:mydatas.picH+'px',width:mydatas.picW+'px',marginRight:mydatas.picR+'px'}" :src="item.pic" />{{item.title}}
                   </div>
               </el-col>
           </el-row> 
      </div>
      <div v-if="type  == 'edit'" >
             <el-tabs v-model="activeName">
                <el-tab-pane label="Button设置" name="first">
                    <select-photo :type="'btn'" :datas="mydatas" v-model="mydatas"></select-photo>

                  
                    <el-form style="padding:10px;" size="mini" label-width="80px">
                       <el-form-item label="内容边距">    
                             <el-slider  :min="0" :max="80"  v-model="mydatas.btnL"></el-slider>
                        </el-form-item>  
                        <el-form-item label="图片宽度">    
                             <el-slider  :min="0" :max="100"  v-model="mydatas.picW"></el-slider>
                        </el-form-item>  
                        <el-form-item label="图片高度">    
                             <el-slider  :min="0" :max="100"  v-model="mydatas.picH"></el-slider>
                        </el-form-item>  
                        <el-form-item label="图片距离文字">    
                             <el-slider  :min="0" :max="30"  v-model="mydatas.picR"></el-slider>
                        </el-form-item>  
                        <el-form-item label="按钮行高">    
                             <el-slider  :min="0" :max="120"  v-model="mydatas.lineH"></el-slider>
                        </el-form-item>

                        <el-form-item label="字体大小">    
                             <el-slider  :min="12" :max="50"  v-model="mydatas.titleSize"></el-slider>
                        </el-form-item>

                        <el-form-item label="按钮间距">    
                             <el-slider  :min="0" :max="100"  v-model="mydatas.mid"></el-slider>
                        </el-form-item>

                        <el-form-item label="字体加粗">    
                                     <el-switch
                                        v-model="mydatas.isB"
                                        :active-value="1"
                                        :inactive-value="0"
                                    >
                                    </el-switch>
                        </el-form-item>
                        

                    </el-form>  
                </el-tab-pane>
                <el-tab-pane label="基本设置" name="second">                      
                     <edit-com :datas="mydatas" v-model="mydatas"></edit-com>
                </el-tab-pane>
               
            </el-tabs>
         
      </div>
  </div>
</template>

<script>
import editCom from '../EditCom.vue';
import SelectLink from  '../SelectLink';
import SelectPhoto from '../SelectPhoto';
export default {
  name: 'pluginButton',
  props: ['type', 'datas'],
  components:{
      editCom,
      SelectLink,
      SelectPhoto
  },
  data(){
      return {
          activeName:'first',
          mydatas:[],
      };
  },
  watch:{
      datas:{
           handler(newName, oldName) {
                if(this.datas != this.mydatas){
                    this.mydatas = this.datas;
                }
            },
            immediate: true,
            deep: true
      },
      mydatas:{
           handler(newName, oldName) {
                if(this.datas != this.mydatas){
                    this.$emit('input',this.mydatas);
                }
            },
            immediate: true,
            deep: true
      }
  },
  methods: {
      getStyle(){
           let style = new Object;
          

           return style;
      },
      getStyleItem(item){
          let style = new Object;
          
          style.fontSize = this.mydatas.titleSize+'px';
          style.lineHeight = this.mydatas.lineH+'px';

          style.paddingLeft = this.mydatas.btnL + 'px';
          style.paddingRight = this.mydatas.btnL + 'px';

          style.background = item.bg;
          style.borderRadius = item.radius + 'px';
          style.color = item.color;
          if(item.shadow){
            style.boxShadow = '-2px 0px 5px 1px '+item.shadowColor+',0px -2px 5px 1px '+item.shadowColor+',2px 0px 5px 1px '+item.shadowColor+',0px 2px 5px 1px '+item.shadowColor;
          }
          if(this.mydatas.isB){
              style.fontWeight = 'bold';
          }  

          return style;
      },
  },
}
</script>
<style scoped>
   .plugin-btn{
       display: flex;
       align-items: center;
        justify-content: center;
   }
</style>