<template>
  <div>
      <div v-if="type == 'show'" class="pluginSearch" :style="getStyle()">
          <el-row>
            <el-col v-if="mydatas.isAction" :span="6">
                    <el-select style="border-radius:0;" size="small" class="right-select" v-model="showTitle" placeholder="请选择">
                            <el-option  label="请选择" :value="''"></el-option>   
                            <el-option v-for="(item,index) in  mydatas.actions" :key="index" :label="item.name" :value="item.desc"></el-option>
                    </el-select>
            </el-col>
            <el-col :span="mydatas.isAction ? 18 : 24">
                 <el-input style="border-radius:0;" size="small"  :placeholder="showTitle!=''?showTitle: mydatas.title">
                         <i slot="prefix" class="el-input__icon el-icon-search"></i>
                 </el-input>
            </el-col>
          </el-row>
           
          

      </div>
      <div v-if="type  == 'edit'">
             <el-tabs v-model="activeName">
                <el-tab-pane label="搜索设置" name="first">
                    <el-form style="padding:10px;" size="mini" label-width="80px">
                        <el-form-item label="输入内容">
                             <el-input type="input" placeholder="请输入内容"   v-model="mydatas.title">
                             </el-input>
                        </el-form-item>
                      
                        <el-form-item   label="边框颜色">  
                            <el-color-picker  v-model="mydatas.bdcolor"  size="mini"></el-color-picker>
                        </el-form-item>
                        <el-form-item   label="搜索框背景">  
                            <el-color-picker  v-model="mydatas.searchBg"  size="mini"></el-color-picker>
                        </el-form-item>

                        <el-form-item label="搜索框圆角">
                            <el-slider  :min="0" :max="60"  v-model="mydatas.bdradius"></el-slider>
                        </el-form-item>    


                        <el-form-item label="显示类型">
                                <el-radio style="margin-left:0px; margin-right:10px;"  v-model="mydatas.isAction" :label="0">不显示</el-radio>
                                <el-radio style="margin-left:0px; margin-right:10px;"  v-model="mydatas.isAction" :label="1">显示</el-radio>    
                        </el-form-item>   
                        <el-form-item v-if="mydatas.isAction == 0" label="默认搜索">
                                <el-select  size="small" v-model="mydatas.defaultAction" placeholder="请选择">
                                        <el-option v-for="(item,index) in  mydatas.actions" :key="index" :label="item.name" :value="item.link"></el-option>
                                </el-select>
                        </el-form-item>  



                    </el-form>  
                </el-tab-pane>


                <el-tab-pane label="基本设置" name="second">                      
                     <edit-com :datas="mydatas" v-model="mydatas"></edit-com>
                </el-tab-pane>
               
            </el-tabs>
         
      </div>
  </div>
</template>

<script>
import editCom from '../EditCom.vue';
export default {
  name: 'pluginSearch',
  props: ['type', 'datas'],
  components:{
      editCom
  },
  data(){
      return {
          activeName:'first',
          mydatas:[],
          showTitle:'',
      };
  },
  watch:{
           
      datas:{
           handler(newName, oldName) {
                if(this.datas != this.mydatas){
                    this.mydatas = this.datas;
                }
            },
            immediate: true,
            deep: true
      },
      mydatas:{
           handler(newName, oldName) {
                if(this.datas != this.mydatas){
                    this.$emit('input',this.mydatas);
                }
            },
            immediate: true,
            deep: true
      }
  },
  methods: {
      getStyle(){
           let style = new Object;
           
           style.border = '1px solid '+this.mydatas.bdcolor;
           style.borderRadius = this.mydatas.bdradius+'px';
           
           style.background = this.mydatas.searchBg;

           return style;
      },
  },
}
</script>
<style>
    .pluginSearch{
        overflow:hidden
    }
  .pluginSearch  .el-input__inner{
        border-radius:0px;
        border:none;
        background:none;
    }
  
</style>

