<template>
  <div>
      <div v-if="type == 'show'"  :style="getStyle()">        
          <el-carousel trigger="click" :height="mydatas.height + 'px'">
            <el-carousel-item v-for="(item,index) in mydatas.pics" :key="index">
                <img :style="'width:100%;height:'+mydatas.height + 'px'"  :src="item.pic ? item.pic: '/miniapp/shop/img/img.png'" />
            </el-carousel-item>
            </el-carousel>
      </div>
      <div v-if="type  == 'edit'" >
             <el-tabs v-model="activeName">
                <el-tab-pane label="轮播设置" name="first">
                   
                   <select-photo :type="'lunbo'" :datas="mydatas" v-model="mydatas"></select-photo>
                    <el-form size="mini" label-width="80px" style="margin-top:10;">
                        <el-form-item label="图片高度">
                             <el-slider  :min="100" :max="375"  v-model="mydatas.height"></el-slider>
                        </el-form-item>
                    </el-form>
                </el-tab-pane>
                <el-tab-pane label="基本设置" name="second">                      
                     <edit-com :datas="mydatas" v-model="mydatas"></edit-com>
                </el-tab-pane>
               
            </el-tabs>
         
      </div>
  </div>
</template>

<script>
import editCom from '../EditCom.vue';
import SelectLink from  '../SelectLink';
import selectPhoto from '../SelectPhoto';
export default {
  name: 'pluginLunbo',
  props: ['type', 'datas'],
  components:{
      editCom,
      SelectLink,
      selectPhoto
  },
  data(){
      return {
          activeName:'first',
          mydatas:[],
      };
  },
  watch:{
      datas:{
           handler(newName, oldName) {
                if(this.datas != this.mydatas){
                    this.mydatas = this.datas;
                }
            },
            immediate: true,
            deep: true
      },
      mydatas:{
           handler(newName, oldName) {
                if(this.datas != this.mydatas){
                    this.$emit('input',this.mydatas);
                }
            },
            immediate: true,
            deep: true
      }
  },
  methods: {
      getStyle(){
           let style = new Object;
           
          
           return style;
      },
  },
}
</script>
