<template>
  <div>
      <div v-if="type == 'show'"  :style="getStyle()">
          <ul  :style="{height:mydatas.lineH + mydatas.bdH + 5 + 'px'}" class="plugin-tab-list">
              <li v-for="(item,index) in mydatas.contents" :key="index" @click="mydatas.selectId = index">
                  <p :style="{lineHeight:mydatas.lineH+'px',color:mydatas.selectId == index ? mydatas.selectColor : mydatas.color,fontSize:mydatas.titleSize + 'px',fontWeight:mydatas.selectId == index  && mydatas.isB?'bold':'100'}">{{item.title}}</p>
                  <div :style="{height:mydatas.bdH+'px',background:mydatas.selectId == index ? mydatas.selectColor:'none'}" class="border">&nbsp;</div>
              </li>
          </ul>
      </div>
      <div v-if="type  == 'edit'" >
             <el-tabs v-model="activeName">
                <el-tab-pane label="Tab设置" name="first">

                    <title-link :datas="mydatas" v-model="mydatas"></title-link>

                    <el-form style="padding:10px;" size="mini" label-width="80px">
                        <el-form-item label="Tab行高">    
                             <el-slider  :min="12" :max="60"  v-model="mydatas.lineH"></el-slider>
                        </el-form-item>     

                         <el-form-item label="tab字号">    
                             <el-slider  :min="12" :max="60"  v-model="mydatas.titleSize"></el-slider>
                        </el-form-item>     
                        <el-form-item   label="tab颜色">  
                            <el-color-picker  v-model="mydatas.color"  size="mini"></el-color-picker>
                        </el-form-item>
                         <el-form-item label="选中加粗">    
                                <el-radio style="margin-left:0px; margin-right:10px;"  v-model="mydatas.isB" :label="0">正常</el-radio>
                                <el-radio style="margin-left:0px; margin-right:10px;"  v-model="mydatas.isB" :label="1">加粗</el-radio>
                        </el-form-item>  
                         <el-form-item   label="选中颜色">  
                            <el-color-picker  v-model="mydatas.selectColor"  size="mini"></el-color-picker>
                        </el-form-item>
                        <el-form-item label="选中边框">    
                             <el-slider  :min="0" :max="5"  v-model="mydatas.bdH"></el-slider>
                        </el-form-item> 
                    </el-form>  
                </el-tab-pane>
                <el-tab-pane label="基本设置" name="second">                      
                     <edit-com :datas="mydatas" v-model="mydatas"></edit-com>
                </el-tab-pane>
               
            </el-tabs>
         
      </div>
  </div>
</template>

<script>
import editCom from '../EditCom.vue';
import SelectLink from  '../SelectLink';
import titleLink from '../TitleLink';
export default {
  name: 'pluginTab',
  props: ['type', 'datas'],
  components:{
      editCom,
      SelectLink,
      titleLink
  },
  data(){
      return {
          activeName:'first',
          mydatas:[],
      };
  },
  watch:{
      datas:{
           handler(newName, oldName) {
                if(this.datas != this.mydatas){
                    this.mydatas = this.datas;
                }
            },
            immediate: true,
            deep: true
      },
      mydatas:{
           handler(newName, oldName) {
                if(this.datas != this.mydatas){
                    this.$emit('input',this.mydatas);
                }
            },
            immediate: true,
            deep: true
      }
  },
  methods: {
      getStyle(){
           let style = new Object;
          

           return style;
      },
  },
}
</script>
<style scoped>
    .plugin-tab-list{
        width:100%;
        overflow:hidden;
    }
    .plugin-tab-list li{
        padding: 0 10px;
        float:left;
    }
    .plugin-tab-list  .border{
        display:block;
        margin: 0 auto;
        margin-top:5px;
        width:80%;
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;
        height:auto;

    }
</style>