<template>
  <div>
      <div v-if="type == 'show'"  :style="getStyle()">        
            <el-row v-if="mydatas.type == 0" :gutter="mydatas.mid">
              <el-col v-for="(item,index) in mydatas.pics" :key="index" :span="12">
                  <div class="plugin-gongge-item" :style="{height:mydatas.height+'px',marginTop:index>=2 ? mydatas.mid+'px' : '0px'}">
                        <img :style="{height:mydatas.height+'px'}" :src="item.pic ? item.pic:'/miniapp/shop/img/img.png'"/>
                  </div>
              </el-col>
            </el-row>

             <el-row v-if="mydatas.type == 1" :gutter="mydatas.mid">
              <el-col v-for="(item,index) in mydatas.pics" :key="index" :span="12">
                  <div class="plugin-gongge-item2" :style="getStyleByItem(item,index)">
                      <div class="left" :style="{marginTop:(mydatas.height - 50)/2 +'px',marginLeft:'10px'}">
                          <p :style="{fontSize:item.titleSize+'px',color:item.titleColor,fontWeight:item.isB ? 'bold' : '100'}">{{item.title}}</p>
                          <p class="desc" :style="{fontSize:item.descSize+'px',color:item.descColor}">{{item.desc}}</p>
                      </div>
                      <img  :style="{marginTop:(mydatas.height - 46)/2  +'px'}" :src="item.pic ? item.pic:'/miniapp/shop/img/img.png'"/>
                  </div>
              </el-col>
            </el-row>

             <el-row v-if="mydatas.type == 2" :gutter="mydatas.mid">
              <el-col v-for="(item,index) in mydatas.pics" :key="index" :span="12">
                  <div class="plugin-gongge-item2" :style="getStyleByItem(item,index)">

                       <img  :style="{marginTop:(mydatas.height - 46)/2  +'px',float:'left',marginLeft:'10px'}" :src="item.pic ? item.pic:'/miniapp/shop/img/img.png'"/>

                      <div class="left" :style="{marginTop:(mydatas.height - 50)/2 +'px'}">
                          <p :style="{fontSize:item.titleSize+'px',color:item.titleColor,fontWeight:item.isB ? 'bold' : '100'}">{{item.title}}</p>
                          <p class="desc" :style="{fontSize:item.descSize+'px',color:item.descColor}">{{item.desc}}</p>
                      </div>
                                   
                  </div>
              </el-col>
            </el-row>
      </div>
      <div v-if="type  == 'edit'">
             <el-tabs v-model="activeName">
                <el-tab-pane label="宫格设置" name="first">
                    <el-form size="mini" label-width="110px" style="margin-top:10;padding-bottom:10px;">
                        <el-form-item  label="单宫格高度">
                             <el-slider  :min="50" :max="200"  v-model="mydatas.height"></el-slider>
                        </el-form-item>

                        <el-form-item label="宫格间距">
                             <el-slider  :min="0" :max="10"  v-model="mydatas.mid"></el-slider>
                        </el-form-item>

                         <el-form-item label="宫格样式">    
                                <el-radio style="margin-left:0px; margin-right:10px;" v-model="mydatas.type" :label="0">样式一</el-radio>
                                <el-radio style="margin-left:0px; margin-right:10px;"  v-model="mydatas.type" :label="1">样式二</el-radio>
                                 <el-radio style="margin-left:0px; margin-right:10px;"  v-model="mydatas.type" :label="2">样式三</el-radio>
                                
                        </el-form-item>  


                    </el-form>

                   <select-photo :type="'gongge'" :datas="mydatas" v-model="mydatas"></select-photo>
                   
                </el-tab-pane>
                <el-tab-pane label="基本设置" name="second">                      
                     <edit-com :datas="mydatas" v-model="mydatas"></edit-com>
                </el-tab-pane>
               
            </el-tabs>
         
      </div>
  </div>
</template>

<script>
import editCom from '../EditCom.vue';
import SelectLink from  '../SelectLink';
import selectPhoto from '../SelectPhoto';
export default {
  name: 'pluginGongge',
  props: ['type', 'datas'],
  components:{
      editCom,
      SelectLink,
      selectPhoto
  },
  data(){
      return {
          activeName:'first',
          mydatas:[],
      };
  },
  watch:{
      datas:{
           handler(newName, oldName) {
                if(this.datas != this.mydatas){
                    this.mydatas = this.datas;
                }
            },
            immediate: true,
            deep: true
      },
      mydatas:{
           handler(newName, oldName) {
                if(this.datas != this.mydatas){
                    this.$emit('input',this.mydatas);
                }
            },
            immediate: true,
            deep: true
      }
  },
  methods: {
      getStyle(){
           let style = new Object;
           
          
           return style;
      },
      
      getStyleByItem(item,index){
           let style = new Object;
           if(index >=2){
               style.marginTop = this.mydatas.mid+'px';
           }
           if(item.bgType == 0){
              style.background = item.bg;
           }else if (item.bgType == 1){            
              style.background = 'linear-gradient('+item.bg+','+item.bg2+')';
           }else{
              style.background = 'linear-gradient(to right,'+item.bg+','+item.bg2+')';
           }
           style.height = this.mydatas.height + 'px';
          
           return style;
      },
  },
}
</script>
<style scoped>
    .plugin-gongge-item{
        width: 100%;
    }
    .plugin-gongge-item img{
        width: 100%;
    }

    .plugin-gongge-item2 img{
        float: right;
        margin-right: 6px;
        width: 46px;
        height: 46px;
    }
    .plugin-gongge-item2 .left{
        float: left;
        padding: 5px;
    }
    .plugin-gongge-item2 .desc{
        margin-top: 5px;
    }
</style>