<template>
  <div>
      <div v-if="type == 'show'"  :style="getStyle()">
         
            <div v-if="mydatas.showType == 2 && showDatas!=''">
                    <el-row :gutter="15">
                      <el-col v-for="(item,index) in showDatas" :key="index" :span="12">
                          <div class="ms-item-2">
                              <img :src="item.photo?item.photo:'/miniapp/shop/img/img.png'" />
                              <div class="djs">{{showTime}}</div>
                          </div>
                          <div :style="{color:mydatas.titleColor,fontWeight:mydatas.isB ? 600:100,fontSize:mydatas.fontSize+'px'}" class="ms-tit">{{item.goods_name}}</div>
                          <div class="price">
                              <span class="pricefh" :style="{color:mydatas.mainColor}" >￥</span>
                              <span :style="{color:mydatas.mainColor}"  class="nprice">{{item.discount_price}}</span>
                              <span :style="{color:mydatas.fzColor}"  class="uprice">￥{{item.shop_price}}</span>
                          </div>
                      </el-col>
                    </el-row>
            </div>
            <div v-if="mydatas.showType == 3 && showDatas!=''">
                 <el-row :gutter="9">
                         <el-col v-for="(item,index) in showDatas" :key="index" :span="8">
                            <div class="ms-item-3">
                              <img :src="item.photo?item.photo:'/miniapp/shop/img/img.png'"/>
                              <div class="djs">{{showTime}}</div>
                          </div>
                          <div class="price">
                              <span  :style="{color:mydatas.mainColor}"  class="pricefh">￥</span><span  :style="{color:mydatas.mainColor}"  class="nprice">{{item.discount_price}}</span> <span  :style="{color:mydatas.fzColor}"  class="uprice">￥{{item.shop_price}}</span>
                          </div>
                        </el-col>
                 </el-row>
            </div>

             <div v-if="showDatas == ''" class="plugin-loadding">{{loading == true ? '数据正在加载中' :'您还未添加内容'}}</div>
      </div>
      <div v-if="type  == 'edit'" >
          <el-form style="padding-bottom:0px;"  size="small" label-width="80px">
                <el-form-item label="样式">
                        <el-radio v-model="mydatas.showType" :label="2">样式一</el-radio>
                        <el-radio v-model="mydatas.showType" :label="3">样式二</el-radio>
                </el-form-item>
                  <el-form-item   label="主要颜色">  
                    <el-color-picker  v-model="mydatas.mainColor"  size="mini"></el-color-picker>
                </el-form-item>
                <el-form-item   label="次要颜色">  
                    <el-color-picker  v-model="mydatas.fzColor"  size="mini"></el-color-picker>
                </el-form-item>   
                <el-form-item   label="标题颜色">  
                    <el-color-picker  v-model="mydatas.titleColor"  size="mini"></el-color-picker>
                </el-form-item>   

                 <el-form-item label="标题加粗">
                        <el-radio v-model="mydatas.isB" :label="0">正常</el-radio>
                        <el-radio v-model="mydatas.isB" :label="1">加粗</el-radio>
                </el-form-item>
                <el-form-item   label="标题字号">    
                        <el-slider  :min="12" :max="60"  v-model="mydatas.fontSize"></el-slider>
                </el-form-item>    

          </el-form>

         <edit-com :datas="mydatas" v-model="mydatas"></edit-com>
      </div>
  </div>
</template>
<style scoped>
  .ms-item-2{
      width: 100%;
      height: 165px;
      position: relative;
  }
  .ms-item-2 img,.ms-item-3 img{
      width: 100%;
      height: 100%;
  }
  .ms-item-2 .djs,.ms-item-3 .djs{
      line-height: 24px;
      width: 100%;
      background: rgba(0,0,0,.6);
      color:#ffffff;
      font-size: 14px;
      position: absolute;
      left: 0px;
      bottom: 0px;
      text-align: center;
  }
  .ms-item-3 .djs{
      font-size: 13px;
  }
  .ms-item-3{
      width: 100%;
      height: 109px;
      position: relative;
  }

  .ms-tit{
        font-size:16px;
        font-weight:600;
        color:rgba(51,51,51,1);
        line-height:22px;
        margin-top: 12px;
        height:44px;
        overflow: hidden;
  }
  .pricefh{
      font-size: 14px;
  }
  .nprice{
      font-size:18px;
      font-weight: 600;
      margin-left: 5px;
  }
  .price{
      margin-top: 12px;
  }
  .uprice{
      margin-left: 10px;
      color:#999999;
      font-size: 14px;
      text-decoration:line-through;
  }
</style>
<script>
import * as api from "@/api";
import editCom from '../EditCom.vue';
export default {
  name: 'pluginCoupon',
  props: ['type','theme', 'datas'],
  components:{
      editCom
  },
  data(){
      return {
          mydatas:[],
          showDatas:[],
          showType:0,
          myThemes:[],
          loading:false,
          endTime:0,
          dh:null,
          showTime:'加载中',
      };
  },
  watch:{
      theme:{
          handler(newName, oldName) {
                if(this.theme != this.myThemes){
                    this.myThemes = this.theme;
                }
            },
            immediate: true,
            deep: true
      },
      datas:{
           handler(newName, oldName) {
                if(this.datas != this.mydatas){
                    this.mydatas = this.datas;
                }
            },
            immediate: true,
            deep: true
      },
      mydatas:{
           handler(newName, oldName) {
                this.getCalldata();
                if(this.datas != this.mydatas){
                    this.$emit('input',this.mydatas);
                }
            },
            immediate: true,
            deep: true
      }
  },
   destroyed(){
      if(this.dh){
            clearTimeout(this.dh);
        }
  },
  methods: {
      getCalldata(){
          if(this.type != 'show' ) return;
          if(this.mydatas.showType != this.showType){
                this.showType = this.mydatas.showType;
                if(this.loading == false){
                         this.loading = true;
                        api.post("/shop/admin.marketing.discount/getCalldata", {
                            appToken: this.$store.getters.appToken,
                            showType:this.showType,
                        }).then(res => {
                            if(res.goods){
                                this.showDatas = res.goods;
                                this.endTime = res.djs;
                                this.djs();
                            }
                            this.loading = false;
                        }).catch((error) => {
                            this.loading = false;
                        });
                }
          }
      },
      djs(){
           if(this.dh){
               clearTimeout(this.dh);
            }
            this.endTime--;
            var h = Math.floor(this.endTime / 3600);
            var min =Math.floor((this.endTime % 3600)/60);
            var s = Math.floor((this.endTime % 3600)%60);;

            if(s < 10){
                s = '0'+s;
            }
            if(min < 10){
                min = '0'+min;
            }
            this.showTime = h + ' : ' + min+' : '+s;
            if(this.endTime > 0){
                 this.dh =  setTimeout(() => {
                    this.djs();
                }, 1000);
            }
      },
      getStyle(){
           let style = new Object;

           return style;
      },
     
  },
}
</script>
