<template>
  <div class="design-main" v-loading="loading">
    <el-container style="height:100%;">
      <el-aside class="design-left" :width="designLeft==0?'68px':'468px'">
         <el-container style="height:100%;">
              <el-aside class="nav" width="68px">
                      <ul class="list">
                            <li  @click="designLeft=!designLeft" :class="designLeft == 0 ? 'logo left' : 'logo'">
                              <i  class="kshicon ksh-icon-jiantou"></i>
                            </li>
                            <li @click="designMdlClick(1)" :class="designMdl==1?'item on':'item'">
                              <i  class="kshicon ksh-icon-zhuti"></i>
                              <span>主题</span>
                            </li>
                            <li  @click="designMdlClick(2)" :class="designMdl==2?'item on':'item'">
                              <i  class="kshicon ksh-icon-dibucaidan"></i>
                              <span>底部</span>
                            </li>
                            <li  @click="designMdlClick(3)" :class="designMdl==3?'item on':'item'">
                              <i  class="kshicon ksh-icon-yemian"></i>
                              <span >页面</span>
                            </li>
                            <li  @click="designMdlClick(4)" :class="designMdl==4?'item on':'item'">
                              <i  class="kshicon ksh-icon-zujian-"></i>
                              <span>插件</span>
                            </li>
                            <li  @click="designMdlClick(5)" :class="designMdl==5?'item on':'item'">
                              <i  class="kshicon ksh-icon-moban"></i>
                              <span>模板</span>
                            </li>
                            <li  class="slide"></li>
                      </ul>
              </el-aside>
            <el-main class="design-left-main">
                  <!--主题设置成功-->
                  <div v-if="designMdl == 1">
                      <div class="header-tit">
                          页面主题 <!--一级标题的样式-->
                      </div>
                      <div class="design-from"  v-loading="themeLoading">

                          <div class="wyu-tips wyu-tips-warning">
                              <p class="tit">注意：首页请使用公共主题编辑。</p>
                          </div>
                          <div style="margin-top: 10px;">
                              <p style="width: 80px;font-size: 14px;display: inline-block;color: #606266">页面标题</p>
                              <el-input style="width: calc(100% - 80px)" :maxlength="10" size="mini" v-model="page.title"></el-input>
                          </div>
                           <el-radio-group style="margin-bottom:10px;margin-top:10px;" size="mini" v-model="page.is_theme">
                              <el-radio-button :label="0">公共主题</el-radio-button>
                              <el-radio-button  :label="1">私有主题</el-radio-button>
                          </el-radio-group>
                          <select-theme v-if="page.is_theme == 0" :theme="theme" v-model="theme"></select-theme>
                          <select-theme v-if="page.is_theme == 1" :theme="page.theme" v-model="page.theme"></select-theme>
                      </div>
                  </div>
                  <!--编辑颜色完成-->
                  <div v-if="designMdl == 2">
                       <div class="header-tit">
                         底部菜单 <!--一级标题的样式-->
                      </div>
                       <div class="design-from" v-loading="themeLoading">

                            <div class="wyu-tips wyu-tips-warning">
                              <p class="tit">注意：首页请使用公共底部编辑。</p>
                            </div>

                            <div style="margin-top:10px;" class="design-sec-tit">
                              <i></i>基本配置
                            </div>
                            <el-radio-group style="margin-bottom:10px;margin-top:10px;" size="mini" v-model="page.footer_type">
                                <el-radio-button :label="0">不使用底部</el-radio-button>
                                <el-radio-button :label="1">公共底部</el-radio-button>
                                <el-radio-button :label="2">私有底部</el-radio-button>
                            </el-radio-group>
                            <el-form v-if="page.footer_type == 2" style="padding-bottom:0px;" size="mini" label-width="80px">


                               <el-form-item label="选中颜色">
                                  <el-color-picker
                                        size="small"
                                        v-model="page.footer.selectColor"
                                        :predefine="predefineColors">
                                      </el-color-picker>
                               </el-form-item>
                            </el-form>

                            <el-form  v-if="page.footer_type == 1" style="padding-bottom:0px;" size="mini" label-width="80px">

                               <el-form-item label="选中颜色">
                                  <el-color-picker
                                        size="small"
                                        v-model="footer.selectColor"
                                        :predefine="predefineColors">
                                      </el-color-picker>
                               </el-form-item>
                            </el-form>


                            <div  v-if="page.footer_type > 0" class="design-sec-tit">
                              <i></i>菜单设置
                            </div>
                            <Icon v-if="page.footer_type == 1" v-model="footer" :icons="footer" :type="'edit'"></Icon>
                            <Icon v-if="page.footer_type == 2" v-model="page.footer" :icons="page.footer" :type="'edit'"></Icon>

                       </div>
                  </div>

                  <!--页面管理-->
                  <div v-if="designMdl == 3">
                        <div class="header-tit">
                          页面管理
                        </div>
                        <div  v-loading="pageLoading" class="design-from">

                            <div class="page-list-block">
                                <div v-for="(item,index) in pageList" :key="index" class="page-list-item">
                                    <img :src="item.photo!='' ? item.photo: '@/assets/img/diy.png' " />
                                    <div class="tit">
                                        {{item.title}}
                                    </div>
                                    <div class="btn">
                                      <el-button   @click="setDefaultPage(item.page_id)" :disabled="item.is_default == 1 ? true:false" size="mini">{{item.is_default == 1 ? '默认首页' : '设为首页'}}</el-button>
                                      <el-button   @click="editPage(item.page_id)"  :disabled="pageId  ==  item.page_id ? true:false"  size="mini">编辑</el-button>
                                    </div>
                                </div>

                            </div>

                             <el-pagination style="margin-top:20px;" @current-change="setPagesPage" :page-size="pageSize"
                                    layout="total, prev, pager, next" :total="pageTotal">
                            </el-pagination>
                        </div>
                  </div>

                  <div v-if="designMdl == 4">
                        <div class="header-tit">
                          页面组件
                        </div>
                        <div  class="design-from">
                              <draggable :list="plugins"
                                       :options="{group:{ name:'plugins', pull:'clone',revertClone:true, put:false}}">
                            <div v-for="(item,index) in plugins" :key="index" class="plugin-item">
                               <i :class=" 'kshicon ' +item.icon "></i>
                               <p>{{item.name}}</p>
                            </div>
                              </draggable>
                        </div>
                  </div>
                   <div v-if="designMdl == 5">
                        <div class="header-tit">
                          模版中心
                        </div>
                          <div v-loading="templateLoading"  class="design-from">
                          <el-radio-group style="margin-bottom:10px;margin-top:10px;" size="mini" @change="getTemplates" v-model="isMyTemplate">
                              <el-radio-button :label="0">模版中心</el-radio-button>
                              <el-radio-button :label="1">我的模版</el-radio-button>
                          </el-radio-group>

                                <div  class="template-list-design">
                                      <div class="item" v-for="(val,key) in templateLists" :key="key">
                                          <div class="template-image-content">
                                              <img class="template-image" :src="val.photo"/>
                                          </div>
                                          <div class="template-info">
                                              <div class="template-title"><span class="template-title-text">{{val.title}}</span></div>
                                              <div class="template-action clearfix">
                                                  <!--class  xm  限免   mf  免费-->
                                                  <span class="xm">限免</span>
                                                  <el-button type="text" size="mini" @click="userTemplate(key)" plain>立即应用</el-button>
                                              </div>
                                          </div>
                                      </div>

                                </div>

                                <el-pagination style="margin-top:20px;" @current-change="setTemplatePage" :page-size="templateSize"
                                        layout="total, prev, pager, next" :total="templateTotal">
                                </el-pagination>

                                </div>
                   </div>
            </el-main>
          </el-container>

      </el-aside>
      <el-main>
          <el-container style="height:100%;">
            <el-header class="design-header" style="height:50px;">
                 <div class="lt">
                    <ul>
                      <li>
                        <router-link to="/dashboard/index"><i class="kshicon ksh-icon-fanhui"></i> 返回小程序管理</router-link>
                      </li>
                      
                    </ul>
                 </div>
                 <div class="rt">
                     <ul>
                         <li  @click="savPageAct(true)" >
                             <i class="kshicon ksh-icon-fabu"></i> 分享
                         </li>
                         <li>
                             <el-button @click="savPageAct(false)" type="primary" icon="el-icon-upload" size="small" round>立即保存</el-button>
                         </li>
                         <li>
                             <router-link to="/dashboard/index"><div class="design-closed"><i class="kshicon ksh-icon-power"></i></div></router-link>
                         </li>
                     </ul>
                </div>
            </el-header>
              <el-container style="height:100%;padding-top:5px;">
                    <el-main>
                          <div class="app-mobile"        >
                                <el-container style="height:100%;">
                                  <el-header class="app-mobile-header"  :style="page.is_theme == 0 ? ('color:'+theme.navigationBarFrontColor+';background:'+theme.navigationBarBackgroundColor+';'):('color:'+page.theme.navigationBarFrontColor+';background:'+page.theme.navigationBarBackgroundColor+';')" height="40px">
                                        <el-container>
                                          <el-aside  class="header-left" width="80px">
                                            <i class="kshicon ksh-icon-jiantou"></i>
                                          </el-aside>
                                          <el-main class="title">{{page.title}}</el-main>
                                          <el-aside class="header-right" width="90px">
                                              <div :style="page.is_theme == 0 ?'border-color:'+theme.navigationBarFrontColor+';':'border-color:'+page.theme.navigationBarFrontColor+';'" class="yuanquan">
                                                <i :style="page.is_theme == 0 ?'border-color:'+theme.navigationBarFrontColor+';':'border-color:'+page.theme.navigationBarFrontColor+';'" class="kshicon ksh-icon-dian"></i>
                                                <i class="kshicon ksh-icon-yuanquan"></i>
                                              </div>
                                          </el-aside>
                                        </el-container>
                                  </el-header>

                                  <el-main class="app-mobile-main"   :style="page.is_theme == 0 ? ('background:'+theme.bgColor+';'):('background:'+page.theme.bgColor+';')">

                                          <draggable :list="page.plugins" @add="plugAdd" @end="plugEnd"
                                            :options="{group:'plugins',animation:400}" style="min-height: 600px;">

                                            <div v-for="(item,key) in page.plugins" :key="key" class="plugin-show-item" :style="getStyle(item)">
                                                <!--显示区域-->
                                                 <component v-bind:is="item.plug" :theme="page.is_theme == 0 ? theme : page.theme" :datas="item.datas" :type="'show'"></component>
                                                 <!--显示区域结束-->
                                                 <div class="action">
                                                  <a href="javascript:void(0);" @click="plugEdit(key)">编辑</a>
                                                  <a href="javascript:void(0);" @click="plugDel(key)">删除</a>
                                                 </div>
                                            </div>
                                          </draggable>

                                  </el-main>




                                  <el-footer v-if="page.footer_type > 0" class="app-mobile-footer"  height="50px">
                                    <Icon :type="'show'" :icons="page.footer_type == 1 ? footer : page.footer"></Icon>
                                  </el-footer>
                                </el-container>
                          </div>
                    </el-main>




                    <el-aside class="design-right" :width="'400px'" :style="isDesignEditor==0 ? 'background:#f2f2f2;' :'background:#ffffff;'">
                          <div v-if="isDesignEditor==1" class="design-right-title">
                              <i @click="isDesignEditor = 0" class="el-icon-close"></i>
                              编辑内容
                          </div>
                          <div v-if="isDesignEditor==1" class="design-from">
                              <div v-for="(item,key) in page.plugins" :key="key">
                                 <!--编辑区域-->
                                    <component v-if="key == pluginId" v-bind:is="item.plug" :datas="item.datas" :type="'edit'"></component>
                                <!--编辑区域结束-->
                              </div>
                          </div>
                    </el-aside>
              </el-container>
          </el-container>
      </el-main>
    </el-container>
      <el-dialog
              v-loading="savePageLoading"
              :modal="false"
              :title="savPage.isShare ? '分享页面' : '保存页面' "
              :visible.sync="dialogSavePage"
              width="500px"
              :modal-append-to-body="false" >
          <el-form size="mini" label-width="80px" style="padding: 0;margin: 0;">
              <el-form-item label="封面预览">
                  <img-uploads :img="savPage.photo" v-model="savPage.photo">  </img-uploads>
              </el-form-item>
              <el-form-item label="页面名称">
                  <el-input v-model="savPage.title"></el-input>
              </el-form-item>
              <el-form-item>
                    <el-button @click="savePagePost">{{savPage.isShare ? '确认分享' : '确认保存'}}</el-button>
              </el-form-item>
          </el-form>
      </el-dialog>
  </div>
</template>

<script>

import draggable from 'vuedraggable';
import selectTheme from './components/SelectTheme';
import Icon from './components/Icon';
import * as api from '@/api.js';
import * as style from './style.js';
import plugins from './plugins.js';

//引入页面组件
import pluginTxt from './components/plugins/PluginTxt.vue';
import pluginPhoto from  './components/plugins/PluginPhoto.vue';
import pluginTitle from  './components/plugins/PluginTitle.vue';
import pluginLunbo from  './components/plugins/PluginLunbo.vue';
import pluginGongge from  './components/plugins/PluginGongge.vue';
import pluginTuwen  from  './components/plugins/PluginTuwen.vue';
import pluginGonggejiu from './components/plugins/PluginGonggejiu.vue';
import pluginSearch from './components/plugins/PluginSearch.vue';
import pluginIconlist from  './components/plugins/PluginIconlist.vue';
import pluginTab from './components/plugins/PluginTab.vue';
import pluginGonggao from './components/plugins/PluginGonggao.vue';
import pluginButton from './components/plugins/PluginButton.vue';
import pluginCoupon from './components/plugins/PluginCoupon.vue';
import pluginMiaosha from './components/plugins/PluginMiaosha.vue';
import pluginPintuan from './components/plugins/PluginPintuan.vue';
import pluginKanjia from './components/plugins/PluginKanjia.vue';
import pluginProduct from './components/plugins/PluginProduct.vue';
import ImgUploads from "@/components/Photos";
export default {
  name: 'design',
  components:{
    ImgUploads,
    selectTheme,  //主题设置
    Icon,
    draggable,
    //页面组件
    pluginTxt,
    pluginPhoto,
    pluginTitle,
    pluginLunbo,
    pluginGongge,
    pluginTuwen,
    pluginGonggejiu,
    pluginSearch,
    pluginIconlist,
    pluginTab,
    pluginGonggao,
    pluginButton,
    pluginMiaosha,
    pluginCoupon,
    pluginPintuan,
    pluginKanjia,
    pluginProduct
  },
  data(){
    return {
       loading:false, //整个页面的LOADING
       themeLoading:false,
       savePageLoading:false, //保存分享页面的 菊花
       dataUrl: '', // 缩略图自动生成的
       dialogSavePage:false,
       pluginId:0,

       plugins:JSON.parse(JSON.stringify(plugins)),

       pageList:[],  //所有的页面
       pagesPage:1,
       pageTotal:0,
       pageSize:10,
       pageLoading:false,
       pageId:0,//当前编辑的ID  非正常的页面ID 都是编辑首页

       //模版市场
       isMyTemplate:0,//是否我的模版 如果是我的模版查询的时候 audit条件不必要
       templateLists:[],
       templateTotal:0,
       templatePage:1,
       templateLoading:false,
       templateSize:10,



       predefineColors:JSON.parse(JSON.stringify(style.predefineColors)),
       designLeft:1,
       designMdl:1,//左侧的模块显示那个
       isDesignEditor:0,
       footer:JSON.parse(JSON.stringify(style.footer)), //默认的底部菜单
       theme:JSON.parse(JSON.stringify(style.theme)), //默认的风格
       pageIsDefault:0, //当前是否是默认页面
       page:{
           photo:'',
           title:'我要有小程序',//标题
           is_theme:1, //是否自定义风格
           footer_type:2,//自定义底部菜单的开关,0不使用 1代表使用默认  2自定义
           theme:{
                navigationBarBackgroundColor:'#000000',//顶部导航的背景颜色
                navigationBarFrontColor:'#ffffff', //顶部导航的字体颜色只能黑色和白色
                mainColor:'#00B5DE',//主要颜色
                secondaryColor:'#1ADCF1',//第二颜色
                tipsColor:'#FFAE19', //提示性的颜色
                statusColor:'#FF3200',//状态颜色
                bgColor:'#f2f2f2',//页面背景颜色
           }, //主题风格
           plugins:[], //底部菜单
           footer:{
                selectColor:'#00B5DE',
                list:[

                ],
           },
       },
        savPage:{
            title:'',
            photo:'',
            isShare: false,
        },
    };
  },
  created(){
      this.getPages();
      this.pageId = this.$route.params.pageid;
      this.getPageDetail(this.pageId);
      this.getTheme();
      this.getTemplates();
  },
  methods:{
    savPageAct(isShare = false) {
        this.dialogSavePage = true;
        this.savPage = {
            title: this.savPage.title ? this.savPage.title:  this.page.title,
            photo:this.page.photo,
            isShare: isShare
        };
    },
    savePagePost(){
      this.savePageLoading = true;
       let theme = JSON.stringify(this.page.theme);
       let footer = JSON.stringify(this.page.footer);
       //是首页 或者 使用的 是公用主题
       if(this.pageIsDefault == 1 || this.page.is_theme == 0){
           theme = JSON.stringify(this.theme);
       }
       //是首页 或者 使用的是 公用底部 不使用底部的情况不考虑
       if(this.pageIsDefault == 1 || this.page.footer_type == 1){
           footer =   JSON.stringify(this.footer);
       }
       let act = this.savPage.isShare ?  "sharePage" : "savePage";
        api.post("/shop/admin.app.pages/" + act, {
            appToken: this.$store.getters.appToken,
            page_id:this.$route.params.pageid,
            title:this.page.title,
            photo:this.savPage.photo,
            page_name:this.savPage.title,
            is_theme:this.page.is_theme,
            theme:theme ,
            plugins:JSON.stringify(this.page.plugins),
            footer_type:this.page.footer_type,
            footer:footer,
        }).then(res => {
            this.savePageLoading = false;
            this.dialogSavePage = false;
            this.$message({
                message: '恭喜你，操作成功',
                type: 'success'
            });
        }).catch((error) => {
            this.savePageLoading = false;
        });
    },
    getStyle(item){
        let style =  new Object;

        if(item.datas.mt){
          style.marginTop = item.datas.mt + 'px';
        }
        if(item.datas.mb){
          style.marginBottom = item.datas.mb + 'px';
        }
        if(item.datas.pl){
          style.paddingLeft = item.datas.pl + 'px';
        }
        if(item.datas.pr){
          style.paddingRight = item.datas.pr + 'px';
        }
        if(item.datas.pt){
          style.paddingTop = item.datas.pt + 'px';
        }
        if(item.datas.pb){
          style.paddingBottom = item.datas.pb + 'px';
        }
        if(item.datas.radius) {
          style.borderRadius = item.datas.radius + 'px';
        }

        if(item.datas.ml > 0){
          style.width = 'calc(100% - '+(item.datas.ml * 2)+'px)';
          style.marginLeft = item.datas.ml+'px';
        }

        if(item.datas.shadow == 1){
          style.boxShadow = '-2px 0px 5px 1px '+item.datas.shadowColor+',0px -2px 5px 1px '+item.datas.shadowColor+',2px 0px 5px 1px '+item.datas.shadowColor+',0px 2px 5px 1px '+item.datas.shadowColor;
        }
        if(item.datas.shadow == 2){
          style.boxShadow = ' 0px 2px 5px' +item.datas.shadowColor ;
        }

        if(item.datas.isBgPic){
          style.backgroundImage = 'url(' + item.datas.bgPic + ')';
          style.backgroundRepeat = 'no-repeat';
          style.backgroundPosition = 'center center';
          style.backgroundSize = '100%';
        }else{
           if(item.datas.bgtype == 0){
              style.background = item.datas.bg;
           }else if (item.datas.bgtype == 1){
              style.background = 'linear-gradient('+item.datas.bg+','+item.datas.bg2+')';
           }else{
              style.background = 'linear-gradient(to right,'+item.datas.bg+','+item.datas.bg2+')';
           }
        }
        return style;
    },
    plugDel(index){
        this.pluginId = 0; //给于0
        this.isDesignEditor = 0;
        this.page.plugins.splice(index,1);
    },

    plugEdit(index){
        this.pluginId = index;
        this.isDesignEditor = 1;
    },

    plugAdd(e){
      this.pluginId = e.newIndex;
      this.page.plugins = JSON.parse(JSON.stringify(this.page.plugins));
      this.isDesignEditor = 1;
    },

    plugEnd(e){
    },

    userTemplate(key){
        if(this.templateLists[key]){
             this.$confirm('此操作会覆盖当前的编辑，您确定要继续吗？', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'info'
            }).then(() => {
                this.isDesignEditor = 0;
                this.pluginId = 0;
                this.page.is_theme = this.templateLists[key].is_theme;
                this.page.footer_type = this.templateLists[key].footer_type;
                this.page.theme = this.templateLists[key].theme ? JSON.parse(JSON.stringify(this.templateLists[key].theme)) : [];
                this.page.footer = this.templateLists[key].footer ? JSON.parse(JSON.stringify(this.templateLists[key].footer)) : [];
                this.page.plugins = this.templateLists[key].plugins ? JSON.parse(JSON.stringify(this.templateLists[key].plugins)) : [];
            }).catch(() => {
            });
        }
    },

    setTemplatePage(page){
        this.templatePage = page;
        this.getTemplates();
    },
    //获取模版
    getTemplates(){
        this.templateLoading = true;
        api.post("/shop/admin.app.templates/index", {
            appToken: this.$store.getters.appToken,
            isown:this.isMyTemplate,
            page:this.templatePage,
            limit:this.templateSize
        }).then(res => {
            this.templateTotal = res.count;
            this.templateLists  = res.list;
            this.templateLoading = false;
        }).catch((error) => {
            this.templateLoading = false;
        });
    },


    //获取默认主题及底部
    getTheme(){
        this.themeLoading = true;
        api.post("/shop/admin.app.pages/getTheme", {
            appToken: this.$store.getters.appToken,
        }).then(res => {
            this.theme = res.detail.theme != '' ? res.detail.theme: JSON.parse(JSON.stringify(style.theme));
            this.footer = res.detail.footer != '' ? res.detail.footer: JSON.parse(JSON.stringify(style.footer));

            this.themeLoading = false;
        }).catch((error) => {
            this.themeLoading = false;
        });
    },
    //获取页面
     getPages(){
        this.pageLoading = true;
        api.post("/shop/admin.app.pages/index", {
            appToken: this.$store.getters.appToken,
            page: this.pagesPage,
            limit:this.pageSize
        }).then(res => {
            this.pageList = res.list;
            this.pageTotal = res.count;
            this.pageLoading = false;
        }).catch((error) => {
            this.pageLoading = false;
        });
     },
     setDefaultPage(pageId){
       this.$confirm('您确定设该页面为首页么？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'info'
        }).then(() => {
              this.pageLoading = true;
            api.post("/shop/admin.app.pages/setDefault", {
                appToken: this.$store.getters.appToken,
                page_id: pageId,
            }).then(res => {
                this.pageLoading = false;
                for(var a in this.pageList){
                    if(this.pageList[a].page_id == pageId){
                      this.pageList[a].is_default = 1;
                    }else{
                      this.pageList[a].is_default = 0;
                    }
                }
            }).catch((error) => {
                this.pageLoading = false;
            });

        }).catch(() => {

        });

     },
     setPagesPage(page){
        this.pagesPage = page;
        this.getPages();
     },
     designMdlClick(index){
       this.designLeft = 1;
       this.designMdl = index;
     },

     editPage(pageId){
        this.$confirm('编辑该页面会覆盖当前的页面，您确定已经保存了本次编辑的结果么？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'info'
        }).then(() => {
           this.getPageDetail(pageId);
        }).catch(() => {});
     },
     getPageDetail(pageId){
       this.isDesignEditor = 0;
        this.loading = true;
            api.post("/shop/admin.app.pages/detail", {
                appToken: this.$store.getters.appToken,
                page_id: pageId,
            }).then(res => {
                this.loading = false;
                this.pageId = res.detail.page_id;
                this.page = {
                    photo:res.detail.photo,
                    title:res.detail.title,//标题
                    is_theme:res.detail.is_theme, //是否自定义风格
                    footer_type:res.detail.footer_type,//自定义底部菜单的开关,0不使用 1代表使用默认  2自定义
                    theme: res.detail.theme != '' ? res.detail.theme : JSON.parse(JSON.stringify(style.theme)),
                    footer: res.detail.footer != '' ? res.detail.footer : JSON.parse(JSON.stringify(style.footer)),
                    plugins: res.detail.plugins != '' ? res.detail.plugins : [],
                };
                this.savPage.title = res.detail.page_name;
                this.pageIsDefault = res.is_default;
            }).catch((error) => {
                this.loading = false;
                this.$router.replace('/dashboard/index');
            });
     },

  },
}
</script>

