<template>
  <div>
      <div v-if="type == 'show'"  :style="getStyle()">
            <el-container>
                <el-main :style="{textAlign:mydatas.type == 2 ? 'center' : 'left'}">
                        <div  class="plugin-title-tit" :style="{fontSize:mydatas.titleSize+'px',lineHeight:mydatas.lineH +'px',color:mydatas.color,fontWeight:mydatas.isB ? 'bold':'100'}">
                           
                            <span v-if="mydatas.type == 1" :style="{height:mydatas.titleSize +'px',background:mydatas.lbcolor,width:mydatas.lbwidth + 'px',marginRight:mydatas.lbmr+'px', display: 'inline-flex'}"></span>

                            <img v-if="mydatas.type == 4" :style="{height:mydatas.iconH+'px',width:mydatas.iconW+'px',marginRight:mydatas.iconR+'px'}" :src="mydatas.icon ? mydatas.icon : '/miniapp/shop/img/img.png'" />
                           
                            {{mydatas.title}}
                        </div>
                        <div v-if="mydatas.type == 3" :style="{fontSize:mydatas.introSize+'px',lineHeight:mydatas.lineHintro +'px',color:mydatas.introColor}">
                           
                            {{mydatas.intro}}

                        </div>

                      
                        <div v-if="mydatas.type == 2" class="plugin-title-desc" :style="{fontSize:mydatas.introSize+'px',lineHeight:mydatas.lineHintro +'px',color:mydatas.introColor}">
                            <span  class="plugin-title-line" v-if="mydatas.isLine" :style="{background:mydatas.introColor}">&nbsp;</span>
                            {{mydatas.intro}}
                            <span class="plugin-title-line" v-if="mydatas.isLine"  :style="{background:mydatas.introColor}">&nbsp;</span>
                        </div>


                </el-main>
                <el-aside v-if="mydatas.more > 0" width="50px" style="text-align:right;">
                    <p v-if="mydatas.more == 1" :style="{fontSize:mydatas.moreSize+'px',lineHeight:mydatas.lineH +'px',color:mydatas.moreColor}">更多</p>
                    <i v-if="mydatas.more == 2"  class="el-icon-arrow-right" :style="{fontSize:mydatas.moreSize+'px',lineHeight:mydatas.lineH +'px',color:mydatas.moreColor}"></i>
                </el-aside>
            </el-container>
      </div>
      <div v-if="type  == 'edit'" >
             <el-tabs v-model="activeName">
                <el-tab-pane label="标题设置" name="first">
                    <el-form style="padding:10px;" size="mini" label-width="80px">

                        <el-form-item label="标题样式">    
                                <el-radio style="margin-left:0px; margin-right:10px;" v-model="mydatas.type" :label="0">默认</el-radio>
                                <el-radio style="margin-left:0px; margin-right:10px;"  v-model="mydatas.type" :label="1">样式一</el-radio>
                                <el-radio style="margin-left:0px; margin-right:10px;"  v-model="mydatas.type" :label="2">样式二</el-radio>
                                <el-radio style="margin-left:0px; margin-right:10px;"  v-model="mydatas.type" :label="3">样式三</el-radio>
                                <el-radio style="margin-left:0px; margin-right:10px;"  v-model="mydatas.type" :label="4">样式四</el-radio>
                        </el-form-item>  

                        <el-form-item label="输入标题">
                             <el-input
                                    type="text"
                                    placeholder="请输入内容"
                                    v-model="mydatas.title">
                            </el-input>
                        </el-form-item>

                         <el-form-item v-if="mydatas.type == 4" label="上传ICON">
                                <wyu-upload :h="40" :w="40"  v-model="mydatas.icon" :img="mydatas.icon"></wyu-upload>
                        </el-form-item>    
                         <el-form-item v-if="mydatas.type == 4" label="ICON高度">    
                             <el-slider  :min="12" :max="60"  v-model="mydatas.iconH"></el-slider>
                        </el-form-item>    

                        <el-form-item v-if="mydatas.type == 4" label="ICON宽度">    
                             <el-slider  :min="12" :max="60"  v-model="mydatas.iconW"></el-slider>
                        </el-form-item>  
                        <el-form-item v-if="mydatas.type == 4" label="距离文字">    
                             <el-slider  :min="0" :max="60"  v-model="mydatas.iconR"></el-slider>
                        </el-form-item>   

                         <el-form-item label="标题字号">    
                             <el-slider  :min="12" :max="60"  v-model="mydatas.titleSize"></el-slider>
                        </el-form-item>     
                        <el-form-item   label="标题颜色">  
                            <el-color-picker  v-model="mydatas.color"  size="mini"></el-color-picker>
                        </el-form-item>
                         <el-form-item label="标题加粗">    
                                <el-radio style="margin-left:0px; margin-right:10px;"  v-model="mydatas.isB" :label="0">正常</el-radio>
                                <el-radio style="margin-left:0px; margin-right:10px;"  v-model="mydatas.isB" :label="1">加粗</el-radio>
                        </el-form-item>  

                        <el-form-item v-if="mydatas.type == 1" label="左边边框">    
                             <el-slider  :min="0" :max="10"  v-model="mydatas.lbwidth"></el-slider>
                        </el-form-item> 
                        <el-form-item v-if="mydatas.type == 1" label="左边边框距离">    
                             <el-slider  :min="0" :max="20"  v-model="mydatas.lbmr"></el-slider>
                        </el-form-item> 
                        <el-form-item v-if="mydatas.type == 1" label="边框背景">    
                            <el-color-picker  v-model="mydatas.lbcolor"  size="mini"></el-color-picker>
                        </el-form-item> 



                        <el-form-item v-if="mydatas.type ==2 || mydatas.type ==3" label="副标题">
                             <el-input
                                    type="text"
                                    placeholder="请输入内容"
                                    v-model="mydatas.intro">
                            </el-input>
                        </el-form-item>
                        <el-form-item  v-if="mydatas.type ==2 || mydatas.type ==3" label="副标题行高">    
                             <el-slider  :min="12" :max="60"  v-model="mydatas.lineHintro"></el-slider>
                        </el-form-item>  
                        <el-form-item  v-if="mydatas.type ==2 || mydatas.type ==3" label="副标题字号">    
                             <el-slider  :min="12" :max="60"  v-model="mydatas.introSize"></el-slider>
                        </el-form-item>    
                        <el-form-item   v-if="mydatas.type ==2 || mydatas.type ==3"  label="副标题颜色">  
                            <el-color-picker  v-model="mydatas.introColor"  size="mini"></el-color-picker>
                        </el-form-item>

                          <el-form-item v-if="mydatas.type == 2" label="显示线条">    
                                <el-radio style="margin-left:0px; margin-right:10px;"  v-model="mydatas.isLine" :label="0">无</el-radio>
                                <el-radio style="margin-left:0px; margin-right:10px;"  v-model="mydatas.isLine" :label="1">显示</el-radio>
                        </el-form-item>  

                        <el-form-item label="更多样式">    
                                <el-radio style="margin-left:0px; margin-right:10px;"  v-model="mydatas.more" :label="0">无</el-radio>
                                <el-radio style="margin-left:0px; margin-right:10px;"  v-model="mydatas.more" :label="1">文字</el-radio>
                                <el-radio style="margin-left:0px; margin-right:10px;"  v-model="mydatas.more" :label="2">箭头</el-radio>
                        </el-form-item>  
                        <el-form-item v-if="mydatas.more > 0" label="更多字号">    
                             <el-slider  :min="12" :max="60"  v-model="mydatas.moreSize"></el-slider>
                        </el-form-item>    
                        <el-form-item v-if="mydatas.more > 0" label="更多链接">    
                               <el-input placeholder="选择链接" v-model="mydatas.link"  class="input-select-link"></el-input>
                               <select-link v-model="mydatas.link"></select-link>
                        </el-form-item>     
                         <el-form-item v-if="mydatas.more > 0"   label="更多颜色">  
                            <el-color-picker  v-model="mydatas.moreColor"  size="mini"></el-color-picker>
                        </el-form-item>    

                         <el-form-item label="底部边框">    
                                <el-radio style="margin-left:0px; margin-right:10px;"  v-model="mydatas.isBt" :label="0">无</el-radio>
                                <el-radio style="margin-left:0px; margin-right:10px;"  v-model="mydatas.isBt" :label="1">有</el-radio>
                        </el-form-item>  
                          <el-form-item  v-if="mydatas.isBt == 1"  label="底部边框颜色">  
                            <el-color-picker  v-model="mydatas.btcolor"  size="mini"></el-color-picker>
                        </el-form-item>   
                      
                       
                      

                    </el-form>  
                </el-tab-pane>
                <el-tab-pane label="基本设置" name="second">                      
                     <edit-com :datas="mydatas" v-model="mydatas"></edit-com>
                </el-tab-pane>
               
            </el-tabs>
         
      </div>
  </div>
</template>

<script>
import editCom from '../EditCom.vue';
import SelectLink from  '../SelectLink';
import WyuUpload from "@/components/Photos";
export default {
  name: 'pluginTitle',
  props: ['type', 'datas'],
  components:{
      editCom,
      SelectLink,
      WyuUpload
  },
  data(){
      return {
          activeName:'first',
          mydatas:[],
      };
  },
  watch:{
      datas:{
           handler(newName, oldName) {
                if(this.datas != this.mydatas){
                    this.mydatas = this.datas;
                }
            },
            immediate: true,
            deep: true
      },
      mydatas:{
           handler(newName, oldName) {
                if(this.datas != this.mydatas){
                    this.$emit('input',this.mydatas);
                }
            },
            immediate: true,
            deep: true
      }
  },
  methods: {
      getStyle(){
           let style = new Object;
           
           if(this.mydatas.isBt){
               style.borderBottom = '1px solid '+ this.mydatas.btcolor;
           }

           return style;
      },
  },
}
</script>
<style scoped>
    .plugin-title-tit{
          display: inline-flex;
          align-items: center;
    }

     .plugin-title-desc{
         display: flex;
         align-items: center;
         justify-content: center;
    }
    .plugin-title-line{
        height: 1px;
        width: 60px;
        margin-right: 20px;
        margin-left: 20px;
        line-height: 1px;
        display: inline-block;
    }
</style>