                            <el-option v-for="(item,index) in selectCats" :label="item.cat_name" :value="item.cat_id" :key="index"></el-option>
                        </el-select>
                </el-form-item>

                <el-form-item  v-if="mydatas.isCat == 1"  label="分类颜色">  
                    <el-color-picker  v-model="mydatas.catColor"  size="mini"></el-color-picker>
                </el-form-item>
                
                <el-form-item   v-if="mydatas.isCat == 1"    label="分类选中">  
                    <el-color-picker  v-model="mydatas.catOn"  size="mini"></el-color-picker>
                </el-form-item>
                <el-form-item  v-if="mydatas.isCat == 1"   label="分类加粗">
                        <el-radio v-model="mydatas.isCatB" :label="1">加粗</el-radio>
                        <el-radio v-model="mydatas.isCatB" :label="0">正常</el-radio>
                </el-form-item>
                <el-form-item  v-if="mydatas.isCat == 1"   label="分类边框">
                        <el-radio v-model="mydatas.isCatBd" :label="1">有</el-radio>
                        <el-radio v-model="mydatas.isCatBd" :label="0">无</el-radio>
                </el-form-item>
                 <el-form-item   v-if="mydatas.isCat == 1"   label="边框颜色">  
                    <el-color-picker  v-model="mydatas.catBdColor"  size="mini"></el-color-picker>
                </el-form-item>
    
                <el-form-item label="标题加粗">
                        <el-radio v-model="mydatas.isB" :label="0">正常</el-radio>
                        <el-radio v-model="mydatas.isB" :label="1">加粗</el-radio>
                </el-form-item>
                 <el-form-item  label="标题字号">    
                        <el-slider  :min="12" :max="18"  v-model="mydatas.fontSize"></el-slider>
                </el-form-item>   
                <el-form-item   label="主要颜色">  
                    <el-color-picker  v-model="mydatas.mainColor"  size="mini"></el-color-picker>
                </el-form-item>
                <el-form-item   label="次要颜色">  
                    <el-color-picker  v-model="mydatas.fzColor"  size="mini"></el-color-picker>
                </el-form-item> 
                 <el-form-item   label="标题颜色">  
                    <el-color-picker  v-model="mydatas.titleColor"  size="mini"></el-color-picker>
                </el-form-item>   
                
                  <el-form-item label="排序方式">
                        <el-radio style="margin-left:0px; margin-right:10px;" v-model="mydatas.order" :label="0">默认</el-radio>
                        <el-radio style="margin-left:0px; margin-right:10px;" v-model="mydatas.order" :label="1">价格</el-radio>
                        <el-radio style="margin-left:0px; margin-right:10px;" v-model="mydatas.order" :label="2">销量</el-radio>
                        <el-radio style="margin-left:0px; margin-right:10px;" v-model="mydatas.order" :label="3">最新</el-radio>
                        <el-radio style="margin-left:0px; margin-right:10px;" v-model="mydatas.order" :label="4">推荐</el-radio>
                </el-form-item>
                <el-form-item  label="商品数量">    
                        <el-slider  :min="1" :max="10"  v-model="mydatas.num"></el-slider>
                </el-form-item>  

          </el-form>

         <edit-com :datas="mydatas" v-model="mydatas"></edit-com>
      </div>
  </div>
</template>
