     .showProduct-1 .price .money{
         font-size: 18px;
         font-weight: 600;
     }
     .showProduct-1 .price2 .yj,.showProduct-1 .price2 .unprice{
         font-size: 12px;
     }
     .showProduct-1 .price2 .unprice{
          text-decoration:line-through;
     }

     .showProduct-1 .ksh-icon-btn_cart{
         float: right;
         font-size: 24px;
     }
    .showProduct-1 .hd-title{
        position:absolute;
        left: 0px;
        bottom: 0px;
        height: 26px;
        line-height: 26px;
        text-align: center;
        color:#fff;
       background:linear-gradient(90deg,rgba(255,164,75,1) 0%,rgba(251,74,134,1) 100%);
        opacity:0.9;
