        font-size: 14px;
    }


.showProduct-2{
     width: 100%;
     height: 100%;
     margin-top: 15px;
 }
  .showProduct-2 .box{
      height:345px;
      overflow: hidden;
      position: relative;
  }

    .showProduct-2 .box img{
        width: 100%;
        height: 100%;
    }
    .showProduct-2 .title{
        width: 100%;
        height: 44px;
        overflow: hidden;
