    }
     .showProduct-2 .price .money{
         font-size: 18px;
         font-weight: 600;
     }
     .showProduct-2 .price .yj,.showProduct-2 .price .unprice{
         font-size: 12px;
     }
      .showProduct-2 .price .yj{
          margin-left: 10px;
      }
     .showProduct-2 .price .unprice{
          text-decoration:line-through;
     }

     .showProduct-2 .ksh-icon-btn_cart{
         float: right;
         font-size: 24px;
     }
   .showProduct-2  .hd-title{
        position:absolute;
        left: 0px;
