    }
</style>
<script>
import * as api from "@/api";
import editCom from '../EditCom.vue';
export default {
  name: 'pluginKanjia',
  props: ['type','theme', 'datas'],
  components:{
      editCom
  },
  data(){
      return {
          mydatas:[],
          showDatas:[],
          num:0,
          loading:false,
          cid:0,
          sid:0,
          sidCache:0,
          showCats:[],
          selectCats:[],
          order:0,
      };
  },
  watch:{
      sid:{
         handler(newName, oldName) {
            this.getCalldata();  
         }, 
      },
      datas:{
           handler(newName, oldName) {
                if(this.datas != this.mydatas){
                    this.mydatas = this.datas;
                }
            },
            immediate: true,
            deep: true
      },
      mydatas:{
           handler(newName, oldName) {
                this.getCalldata();
               
                if(this.datas != this.mydatas){
                    this.$emit('input',this.mydatas);
                }
            },
            immediate: true,
            deep: true
      },
      
      
  },
 created(){
      this.getCats();
 },
  methods: {
      getCats(){
          if(this.type == 'show' ) return;
          if(this.selectCats == ''){
              if(this.loading == false){
                    this.loading = true;
                    api.post("/shop/admin.goods.goods/getPcatlist", {
                        appToken: this.$store.getters.appToken,
                    }).then(res => {
                        if(res.cats){
                            this.selectCats = res.cats;
                        }
                        this.loading = false;
                    }).catch((error) => {
                        this.loading = false;
                    });
                }
          }
      },
      getCalldata(){
          if(this.type != 'show' ) return;
          if(this.mydatas.num != this.num || this.order != this.mydatas.order || this.sid != this.sidCache || this.mydatas.cid != this.cid){
                this.num = this.mydatas.num;
                this.cid = this.mydatas.cid;//父级ID    
                this.sidCache = this.sid;
                this.order = this.mydatas.order;
                if(this.loading == false){
                         this.loading = true;
                        api.post("/shop/admin.goods.goods/getCalldata", {
                            appToken: this.$store.getters.appToken,
                            num:this.num,
                            cid:this.cid,
                            order:this.order,
                            sid:this.sid,
                        }).then(res => {
                            if(res.goods){
                                this.showDatas = res.goods;
                                
                            }
                            if(res.cats){
                                this.showCats = res.cats;
                            }
                            this.loading = false;
                        }).catch((error) => {
                            this.loading = false;
                        });
                }
          }
      },
     
      getStyle(){
           let style = new Object;

           return style;
      },
     
  },
}
</script>
