<template>
  <div>
      <div v-if="type == 'show'"  :style="getStyle()">
          <el-container>
               <el-aside :width="mydatas.ggW > 50 ? mydatas.ggW + 'px' :'50px'" style="display:flex; align-items: center;padding-left:10px;">
                   <img :style="{width:(mydatas.ggW -10) +'px',height:mydatas.ggH+'px'}" :src="mydatas.img ? mydatas.img : '/miniapp/shop/img/img.png'" />
               </el-aside>
               <el-main :style="'height:'+mydatas.height+'px;overflow:hidden;	position: relative;'" >
                   <ul :style="{top:-top + 'px',fontSize:mydatas.titleSize+'px',color:mydatas.color,fontWeight:mydatas.isB ? 'bold' : '100'}" class="marquee_list">
                       <li style="padding-left:10px;" v-for="(item,index) in showDatas" :key="index">{{item.title}}</li>
                   </ul>
               </el-main>
               <el-aside width="40px" v-if="mydatas.isMore == 1" style="text-align:right;">
                   <i :style="{fontSize:mydatas.moreSize+'px',color:mydatas.moreColor}" class="el-icon-arrow-right"></i>
               </el-aside>
          </el-container>
      </div>
      <div v-if="type  == 'edit'" >
             <el-tabs v-model="activeName">
                <el-tab-pane label="公告设置" name="first">

                    <title-link :datas="mydatas" v-model="mydatas"></title-link>

                    <el-form style="padding:10px;" size="mini" label-width="100px">
                        <el-form-item label="公告行高">    
                             <el-slider  :min="40" :max="100"  v-model="mydatas.height"></el-slider>
                        </el-form-item>     
                         <el-form-item label="公告图标">
                                 <wyu-upload :h="40" :w="40"  v-model="mydatas.img" :img="mydatas.img"></wyu-upload>
                         </el-form-item>
                        <el-form-item label="公告图标宽度">    
                             <el-slider  :min="12" :max="100"  v-model="mydatas.ggW"></el-slider>
                        </el-form-item>   
                        <el-form-item label="公告图标高度">    
                             <el-slider  :min="12" :max="mydatas.height"  v-model="mydatas.ggH"></el-slider>
                        </el-form-item>     

                         <el-form-item label="公告字号">    
                             <el-slider  :min="12" :max="60"  v-model="mydatas.titleSize"></el-slider>
                        </el-form-item>    
                 
                         <el-form-item label="公告内容圆角">    
                             <el-slider   v-model="mydatas.ggRadius"></el-slider>
                        </el-form-item>   
                        <el-form-item   label="公告内容背景">  
                            <el-color-picker  v-model="mydatas.ggBg"  size="mini"></el-color-picker>
                        </el-form-item>
                        <el-form-item   label="公告颜色">  
                            <el-color-picker  v-model="mydatas.color"  size="mini"></el-color-picker>
                        </el-form-item>

                         <el-form-item label="公告加粗">    
                                <el-radio style="margin-left:0px; margin-right:10px;"  v-model="mydatas.isB" :label="0">正常</el-radio>
                                <el-radio style="margin-left:0px; margin-right:10px;"  v-model="mydatas.isB" :label="1">加粗</el-radio>
                        </el-form-item>  
                    
                         <el-form-item label="显示更多">    
                                <el-radio style="margin-left:0px; margin-right:10px;"  v-model="mydatas.isMore" :label="0">不显示</el-radio>
                                <el-radio style="margin-left:0px; margin-right:10px;"  v-model="mydatas.isMore" :label="1">显示</el-radio>
                        </el-form-item> 

                         <el-form-item  v-if="mydatas.isMore == 1" label="更多字号">    
                                <el-slider  :min="12" :max="60"  v-model="mydatas.moreSize"></el-slider>
                        </el-form-item> 

                         <el-form-item  v-if="mydatas.isMore == 1"   label="更多颜色">  
                            <el-color-picker  v-model="mydatas.moreColor"  size="mini"></el-color-picker>
                        </el-form-item>
                        <el-form-item  v-if="mydatas.isMore == 1" label="更多链接">
                              <el-input v-model="mydatas.moreLink" size="mini" style="width:80px;float:left; margin-left:10px; margin-right:10px;" placeholder="选择链接"></el-input>
                             <select-link  v-model="mydatas.moreLink"></select-link>
                        </el-form-item>

                          <el-form-item label="内容投影">
                                    <el-radio v-model="mydatas.ggshadow" :label="0">无</el-radio>
                                    <el-radio v-model="mydatas.ggshadow" :label="1">四周</el-radio>
                                    <el-radio v-model="mydatas.ggshadow" :label="2">底部</el-radio>
                            </el-form-item>   
                            <el-form-item   v-if="mydatas.ggshadow > 0"  label="内容投影颜色">  
                            <el-color-picker  v-model="mydatas.ggshadowColor"  size="mini"></el-color-picker>
                            </el-form-item>

                                

                    </el-form>  
                </el-tab-pane>
                <el-tab-pane label="基本设置" name="second">                      
                     <edit-com :datas="mydatas" v-model="mydatas"></edit-com>
                </el-tab-pane>
               
            </el-tabs>
         
      </div>
  </div>
</template>

<script>
import editCom from '../EditCom.vue';
import SelectLink from  '../SelectLink';
import WyuUpload from "@/components/Photos";
import titleLink from '../TitleLink';
export default {
  name: 'pluginGonggao',
  props: ['type', 'datas'],
  components:{
      editCom,
      SelectLink,
      titleLink,
      WyuUpload
  },
  data(){
      return {
          activeName:'first',
          mydatas:[],
          showDatas:[],
          dh:null,
          showIndex:0,
          top:0,
      };
  },
  watch:{
      datas:{
           handler(newName, oldName) {
                if(this.datas != this.mydatas){
                    this.mydatas = this.datas;
                }
            },
            immediate: true,
            deep: true
      },
      mydatas:{
           handler(newName, oldName) {
                this.showDatas = JSON.parse(JSON.stringify(this.mydatas.contents));
                if(this.datas != this.mydatas){
                    this.$emit('input',this.mydatas);
                }
            },
            immediate: true,
            deep: true
      }
  },
  created(){
      if(this.mydatas.contents){
          this.showDatas = JSON.parse(JSON.stringify(this.mydatas.contents));
      }
      
      if(this.type == 'show'){
          this.showGundong();
      }

  },
  destroyed(){
      if(this.dh){
            clearTimeout(this.dh);
        }
  },
  methods: {
      showGundong(){
            if(this.dh){
               clearTimeout(this.dh);
            }
            this.top = this.showIndex * this.mydatas.height;
            if(this.showIndex < this.showDatas.length -1){
                this.showIndex ++;
            }else{
                this.showIndex = 0;
            }
           this.dh =  setTimeout(() => {
                this.showGundong();
           }, 2000);
      },
      getStyle(){
           let style = new Object;
         
            if(this.mydatas.ggRadius){
                style.borderRadius = this.mydatas.ggRadius + 'px';
            }
            if(this.mydatas.ggBg){
                style.background = this.mydatas.ggBg;
            }
            style.lineHeight = this.mydatas.height + 'px';

            if(this.mydatas.ggshadow == 1){
                 style.boxShadow = '-2px 0px 5px 1px '+this.mydatas.ggshadowColor+',0px -2px 5px 1px '+this.mydatas.ggshadowColor+',2px 0px 5px 1px '+this.mydatas.ggshadowColor+',0px 2px 5px 1px '+this.mydatas.ggshadowColor;
            }
            if(this.mydatas.ggshadow == 2){
                 style.boxShadow = ' 0px 2px 5px' +this.mydatas.ggshadowColor ;
            }
           return style;
      },
  },
}
</script>
<style scoped>
.marquee_list{
    transition: all 0.5s;
    position: absolute;
	top: 0;
	left: 0;
}

</style>