<template>
  <div class="select-theme">
      <el-form size="mini"  label-width="120px">
           <el-form-item label="导航颜色">
              <el-color-picker
                size="small"
                v-model="mytheme.navigationBarBackgroundColor"
                :predefine="predefineColors">
              </el-color-picker>
           </el-form-item>

           <el-form-item label="导航字体颜色">
                  <el-radio v-model="mytheme.navigationBarFrontColor" label="#000000">黑色</el-radio>
                  <el-radio v-model="mytheme.navigationBarFrontColor" label="#ffffff">白色</el-radio>
           </el-form-item>

            <el-form-item label="主要颜色">
              <el-color-picker
                size="small"
                v-model="mytheme.mainColor"
                :predefine="predefineColors">
              </el-color-picker>
           </el-form-item>
            <el-form-item label="辅助颜色">
              <el-color-picker
                size="small"
                v-model="mytheme.secondaryColor"
                :predefine="predefineColors">
              </el-color-picker>
           </el-form-item>
          <el-form-item label="提示性颜色">
              <el-color-picker
                size="small"
                v-model="mytheme.tipsColor"
                :predefine="predefineColors">
              </el-color-picker>
           </el-form-item>
          <el-form-item label="状态颜色">
              <el-color-picker
                size="small"
                v-model="mytheme.statusColor"
                :predefine="predefineColors">
              </el-color-picker>
           </el-form-item>

           <el-form-item label="页面背景颜色">
              <el-color-picker
                size="small"
                v-model="mytheme.bgColor"
                :predefine="predefineColors">
              </el-color-picker>
           </el-form-item>
      </el-form> 
  </div>
</template>

<script>
import  *  as style from '../style.js';
export default {
  name: 'HelloWorld',
  props: ['theme'],
  data(){
      return {
          predefineColors: JSON.parse(JSON.stringify(style.predefineColors)),
          mytheme:JSON.parse(JSON.stringify(style.theme)),
      };
  },
  watch:{
      theme:{
            handler(newObj,oldObj) {
                if(this.mytheme != this.theme){
                    this.mytheme = this.theme;
                }
            },
            immediate: true,
            deep: true
      },
      mytheme:{
            handler(newObj,oldObj) {
                if(this.mytheme != this.theme){
                     this.$emit('input',this.mytheme);
                }
            },
            immediate: true,
            deep: true
      }
  },
  created(){
     
  },
  methods:{
      
  },
}
</script>

<style scoped>

</style>
