<template>
  <div style="padding:10px;">
    <el-form  size="mini" label-width="80px">

          <el-form-item label="背景类型">
                 <el-radio v-model="mydatas.isBgPic" :label="0">颜色背景</el-radio>
                 <el-radio v-model="mydatas.isBgPic" :label="1">图片背景</el-radio>
        </el-form-item>     
         <el-form-item   v-if="mydatas.isBgPic == 1" label="背景图片">
                <wyu-upload  v-model="mydatas.bgPic" :img="mydatas.bgPic"></wyu-upload>
         </el-form-item> 
         <el-form-item   v-if="mydatas.isBgPic == 0" label="背景样式">
                <el-radio v-model="mydatas.bgtype" :label="0">纯色</el-radio>
                <el-radio v-model="mydatas.bgtype" :label="1">上下渐变</el-radio>
                <el-radio v-model="mydatas.bgtype" :label="2">左右渐变</el-radio>
         </el-form-item> 
        <el-form-item  v-if="mydatas.isBgPic == 0"  label="背景颜色">  
           <el-color-picker  v-model="mydatas.bg"  size="mini"></el-color-picker>
        </el-form-item>
        <el-form-item   v-if="mydatas.bgtype > 0"  label="渐变背景">  
           <el-color-picker  v-model="mydatas.bg2"  size="mini"></el-color-picker>
        </el-form-item>
          
          <el-form-item label="投影">
                 <el-radio v-model="mydatas.shadow" :label="0">无</el-radio>
                 <el-radio v-model="mydatas.shadow" :label="1">四周</el-radio>
                 <el-radio v-model="mydatas.shadow" :label="2">底部</el-radio>
        </el-form-item>   
          <el-form-item   v-if="mydatas.shadow > 0"  label="投影颜色">  
           <el-color-picker  v-model="mydatas.shadowColor"  size="mini"></el-color-picker>
        </el-form-item>




        <el-form-item label="距离顶部">
             <el-slider v-model="mydatas.mt"></el-slider>
        </el-form-item>
        <el-form-item label="距离下面">
             <el-slider v-model="mydatas.mb"></el-slider>
        </el-form-item>
         <el-form-item label="距离左边">
             <el-slider v-model="mydatas.ml"></el-slider>
        </el-form-item>
        <el-form-item label="左内边距">
             <el-slider v-model="mydatas.pl"></el-slider>
        </el-form-item>
        <el-form-item label="右内边距">
             <el-slider v-model="mydatas.pr"></el-slider>
        </el-form-item>
        <el-form-item label="上内边距">
             <el-slider v-model="mydatas.pt"></el-slider>
        </el-form-item>
        <el-form-item label="下内边距">
             <el-slider v-model="mydatas.pb"></el-slider>
        </el-form-item>

        <el-form-item label="圆角">
             <el-slider v-model="mydatas.radius"></el-slider>
        </el-form-item>

           


    </el-form>   
  </div>
</template>

<script>
import WyuUpload from "@/components/Photos";

export default {
  name: 'pluginTxt',
  props: ['datas'],
  components:{
      WyuUpload
  },
  data(){
      return {
          mydatas:[],
      };
  },
  watch:{
      datas:{
           handler(newName, oldName) {
                if(this.datas != this.mydatas){
                    this.mydatas = this.datas;
                }
            },
            immediate: true,
            deep: true
      },
      mydatas:{
           handler(newName, oldName) {
                if(this.datas != this.mydatas){
                    this.$emit('input',this.mydatas);
                }
            },
            immediate: true,
            deep: true
      }
  },
  methods: {
      
  },
}
</script>
