<template>
  <div style="padding:5px;">

        <div v-for="(item,index) in mydatas.contents" :key="index" style="height: 80px;" class="select-contents-item">
            <el-container>
                <el-main class="select-contents-main">
                    <div class="select-contents-col">
                        <span class="tit">标题</span>
                        <el-input v-model="mydatas.contents[index].title" style="width:180px;float:left; margin-left:10px; margin-right:10px;"  size="mini"  placeholder="输入内容"></el-input>
                    </div>
                    <div class="select-contents-col">
                        <span class="tit">链接</span>
                        <el-input v-model="mydatas.contents[index].link" size="mini" style="width:80px;float:left; margin-left:10px; margin-right:10px;" placeholder="选择链接"></el-input>
                        <select-link  v-model="mydatas.contents[index].link"></select-link>
                    </div>
                    <i @click="delItem(index)" class="el-icon-circle-close"></i>
                </el-main>
            </el-container>       
        </div>
           

    <el-button @click="addItem" type="text">添加一条</el-button>    
  </div>
</template>

<script>
import SelectLink from  './SelectLink';
import WyuUpload from "@/components/Photos";


export default {
  name: 'titleLink',
  props: ['datas'],
  components:{
      SelectLink,
      WyuUpload
  },
  data(){
      return {
          mydatas:[],
      };
  },
  watch:{
      datas:{
           handler(newName, oldName) {
                if(this.datas != this.mydatas){
                    this.mydatas = this.datas;
                }
            },
            immediate: true,
            deep: true
      },
      mydatas:{
           handler(newName, oldName) {
                if(this.datas != this.mydatas){
                    this.$emit('input',this.mydatas);
                }
            },
            immediate: true,
            deep: true
      }
  },
  created() {
      this.mydatas = this.datas;
  },
  methods: {
      delItem(index){
          this.mydatas.contents.splice(index,1);
      },
      addItem(){
            this.mydatas.contents.push({
                title:'自定义',link:''
            });    
      },
  },
}
</script>
<style scoped>
    .select-contents-item{
        width: 100%;
        border: 1px solid #DCDFE6;
        margin-top: 5px;
        position: relative;
        background: #F2F6FC;
    }
 
    .select-contents-main i{
        position:absolute;
        right: -5px;
        top: -5px;
        color: #999999;
        font-size: 16px;
        cursor: pointer;
    }
    .select-contents-col{
        height: 30px;
        line-height: 30px;
        margin-top:5px;
        font-size: 13px;
    }

    .select-contents-col .tit{ float: left; margin-left: 5px;}
</style>
