<template>
  <div>
      <div v-if="type == 'show'"  :style="getStyle()">
          <div v-if="mydatas.showType == 1 && showDatas !=''">
                <div v-for="(item,index) in  showDatas" :key="index" class="coupon-type-1" >
                    <div :style="{borderColor:mydatas.borderColor,background:mydatas.couponBg}"  class="box">
                        <div :style="{borderColor:mydatas.borderColor}" class="tblock">
                            <el-row>
                            <el-col  :span="8">
                                <div :style="{color:mydatas.mainColor}" class="money">
                                ￥<span>{{item.money}}</span>
                                </div>  
                            </el-col>
                            <el-col :span="16">
                                <div class="coupon-content">
                                        <h2 :style="{color:mydatas.titleColor}">{{item.name}}</h2>
                                        <p :style="{color:mydatas.fzColor}" class="info">{{item.userarea}}</p>
                                        <p :style="{color:mydatas.titleColor}" class="t">{{item.expire_start_time}}-{{item.expire_end_time}}</p>
                                </div>
                            </el-col>
                            </el-row>
                        </div>
                        <div :style="{color:mydatas.mainColor}" class="lj-btn">立即领取</div>
                    </div>    
                    <span  :style="{borderColor:mydatas.borderColor,background:mydatas.bg}"  class="yuan"></span>
                    <span :style="{borderColor:mydatas.borderColor,background:mydatas.bg}"  class="yuan2"></span>
                </div>
          </div>
            <div v-if="mydatas.showType == 3 && showDatas !=''">
                    <el-row :gutter="15">
                      <el-col  v-for="(item,index) in  showDatas" :key="index"  :span="8">
                          <div class="coupon-type-2">
                              <div :style="{borderColor:mydatas.borderColor,background:mydatas.couponBg}" class="box">
                                  <div :style="{borderColor:mydatas.borderColor}" class="tblock">
                                         <div :style="{color:mydatas.mainColor}" class="money">
                                        ￥<span>{{item.money}}</span>
                                        </div>  
                                  </div>
                                   <h2 :style="{color:mydatas.titleColor}">{{item.name}}</h2>
                                   <div :style="{color:mydatas.mainColor,borderColor:mydatas.mainColor}" class="btn">去领取</div>
                              </div>
                            <span  :style="{borderColor:mydatas.borderColor,background:mydatas.bg}"  class="yuan"></span>
                            <span :style="{borderColor:mydatas.borderColor,background:mydatas.bg}"  class="yuan2"></span>
                          </div>
                      </el-col>
                  
                    </el-row>
            </div>
            <div v-if="showDatas == ''" class="plugin-loadding">{{loading == true ? '数据正在加载中' :'您还未添加内容'}}</div>

      </div>
      <div v-if="type  == 'edit'" >
          <el-form style="padding-bottom:0px;" size="small" label-width="80px">
                <el-form-item label="样式">
                        <el-radio v-model="mydatas.showType" :label="1">样式一</el-radio>
                        <el-radio v-model="mydatas.showType" :label="3">样式二</el-radio>
                </el-form-item>
                 <el-form-item label="排序方式">
                        <el-radio style="margin-left:0px; margin-right:10px;" v-model="mydatas.order" :label="0">默认</el-radio>
                        <el-radio style="margin-left:0px; margin-right:10px;" v-model="mydatas.order" :label="1">金额最高</el-radio>
                        <el-radio style="margin-left:0px; margin-right:10px;" v-model="mydatas.order" :label="2">下载量</el-radio>
                        <el-radio style="margin-left:0px; margin-right:10px;" v-model="mydatas.order" :label="3">使用最多</el-radio>
                        <el-radio style="margin-left:0px; margin-right:10px;" v-model="mydatas.order" :label="4">推荐排序</el-radio>
                </el-form-item>

                <el-form-item   label="主要颜色">  
                    <el-color-picker  v-model="mydatas.mainColor"  size="mini"></el-color-picker>
                </el-form-item>
                <el-form-item   label="次要颜色">  
                    <el-color-picker  v-model="mydatas.fzColor"  size="mini"></el-color-picker>
                </el-form-item>   
                <el-form-item   label="标题颜色">  
                    <el-color-picker  v-model="mydatas.titleColor"  size="mini"></el-color-picker>
                </el-form-item>    
                 <el-form-item   label="边框颜色">  
                    <el-color-picker  v-model="mydatas.borderColor"  size="mini"></el-color-picker>
                </el-form-item>   
                  <el-form-item   label="内容背景">  
                    <el-color-picker  v-model="mydatas.couponBg"  size="mini"></el-color-picker>
                </el-form-item>   

          </el-form>

         <edit-com :datas="mydatas" v-model="mydatas"></edit-com>
      </div>
  </div>
</template>

<script>
import * as api from "@/api";
import editCom from '../EditCom.vue';
export default {
  name: 'pluginCoupon',
  props: ['type', 'datas'],
  components:{
      editCom
  },
  data(){
      return {
          mydatas:[],
          showDatas:[],
          showType:0,
          order:0,
          loading:false,
      };
  },
  watch:{
    
      datas:{
           handler(newName, oldName) {
                if(this.datas != this.mydatas){
                    this.mydatas = this.datas;
                }
            },
            immediate: true,
            deep: true
      },
      mydatas:{
           handler(newName, oldName) {
                this.getCalldata();
                if(this.datas != this.mydatas){
                    this.$emit('input',this.mydatas);
                }
            },
            immediate: true,
            deep: true
      }
  },
  methods: {
      getCalldata(){
          if(this.type != 'show' ) return;
          if(this.mydatas.showType != this.showType ||  this.mydatas.order != this.order){
                this.showType = this.mydatas.showType;
                this.order = this.mydatas.order;
                if(this.loading == false){
                         this.loading = true;
                        api.post("/shop/admin.marketing.coupon/getCalldata", {
                            appToken: this.$store.getters.appToken,
                            order:this.order,
                            showType:this.showType,
                        }).then(res => {
                            this.showDatas = res.coupons;
                            this.loading = false;
                        }).catch((error) => {
                            this.loading = false;
                        });
                }
          }
      },
      getStyle(){
           let style = new Object;

           return style;
      },
     
  },
}
</script>
<style scoped>
  
   .coupon-type-1{
        position: relative;
        width:100%;
        overflow: hidden;
        height:144px;
        
   }
   .coupon-type-2{
        position: relative;
        width:100%;
        overflow: hidden;
        height:172px;
   }
   .coupon-type-2 .box{
        width: 100%;
        height: 100%;
        background:rgba(255,255,255,1);
        border:1px solid rgba(204,204,204,1);
   }
   .coupon-type-1 .box{
        width: 100%;
        height: 100%;
        background:rgba(255,255,255,1);
        border-radius:8px;
        border:1px solid rgba(204,204,204,1);
   }
   .coupon-type-1 .tblock{
       width: 96%;
       margin: 0 auto;
       height: 98px;
       border-bottom: 1px dashed #CCCCCC
   }
   .coupon-type-1 .money{
       line-height: 96px;
       text-align: center;
       font-weight: bold;
       font-size: 16px;
   }
   .coupon-type-1 .money span{
       font-size: 24px;
   }
   .coupon-content{
       padding-left: 10px;
   }
    .coupon-type-1 h2{
        font-size: 16px;
        margin-top: 16px;
    }
    .coupon-type-1 .info{
        font-size: 12px;
        margin-top: 12px;
        color:#666666;
    }
    .coupon-type-1 .t{
        margin-top: 8px;
        font-size: 12px;
    }
    .lj-btn{
        margin-top:13px;
        font-size: 18px;
        font-weight: 600;
        text-align: center;
    }
    .coupon-type-1 .yuan,  .coupon-type-1 .yuan2{
        width: 16px;
        height: 16px;
        border:1px solid rgba(204,204,204,1);
        position:absolute;
        
        top:89px;
        border-radius: 8px;
        background: #fff;
    }
     .coupon-type-1 .yuan{
            left: -8px;
     }
     .coupon-type-1 .yuan2{
         right: -8px;
     }

    .coupon-type-2 .yuan,  .coupon-type-2 .yuan2{
        width: 16px;
        height: 16px;
        border:1px solid rgba(204,204,204,1);
        position:absolute;
        top:59px;
        border-radius: 8px;
        background: #fff;
    }
     .coupon-type-2 .yuan{
            left: -8px;
     }
     .coupon-type-2 .yuan2{
         right: -8px;
     }
     
    .coupon-type-2 .tblock{
       width: 96%;
       margin: 0 auto;
       height: 68px;
       border-bottom: 1px dashed #CCCCCC;
       padding-top:20px;
   }
   .coupon-type-2 .money{
       width: 100%;
       text-align: center;
       font-weight: bold;
       font-size: 16px;
   }
   .coupon-type-2 .money span{
       font-size: 20px;
   }
   .coupon-type-2 h2{
       font-size:16px;
       color: #000000;
       font-weight: 400;
       margin-top: 18px;
       text-align: center;
       width: 100%;
       overflow: hidden;
       height: 20px;
   }
  .coupon-type-2 .btn{
      width: 70%;
      text-align: center;
      line-height: 26px;
      border:1px solid #f2f2f2;
      font-size: 14px;
      font-weight: 600;
      margin: 0 auto;
      margin-top: 16px;
  }
</style>