<template>
  <div>
      <div v-if="type == 'show'"  :style="getStyle()">        

            <el-row  :gutter="mydatas.mid">
              <el-col v-for="(item,index) in mydatas.pics" :key="index" :span="24/(mydatas.num ? mydatas.num : 2)">
                  <div class="plugin-tuwen-item" :style="{height:mydatas.height+'px',marginTop:index>=mydatas.num ? mydatas.mid+'px' : '0px'}">
                      <div :style="{height:mydatas.type == 0 ? mydatas.height - mydatas.titleH+'px' : mydatas.height+'px',width:'100%',background:'#000'}">
                             <img :style="{height:'100%',width:'100%'}" :src="item.pic?item.pic:'/miniapp/shop/img/img.png'" />
                      </div>
                      <div :style="getStyleByItem(item)">
                             {{item.title}}
                      </div>
                  </div>
              </el-col>
            </el-row>


      </div>
      <div v-if="type  == 'edit'" >
             <el-tabs v-model="activeName">
                <el-tab-pane label="图文设置" name="first">

                    <select-photo :type="'default'" :datas="mydatas" v-model="mydatas"></select-photo>

                    <el-form size="mini" label-width="110px" style="margin-top:10;padding-bottom:10px;">
                        <el-form-item  label="单图文高度">
                             <el-slider  :min="50" :max="200"  v-model="mydatas.height"></el-slider>
                        </el-form-item>

                        <el-form-item label="图文间距">
                             <el-slider  :min="0" :max="10"  v-model="mydatas.mid"></el-slider>
                        </el-form-item>

                        <el-form-item label="每行数量">
                             <el-slider  :min="2" :max="4"  v-model="mydatas.num"></el-slider>
                        </el-form-item>

                         <el-form-item label="图文样式">    
                                <el-radio style="margin-left:0px; margin-right:10px;" v-model="mydatas.type" :label="0">默认</el-radio>
                                <el-radio style="margin-left:0px; margin-right:10px;"  v-model="mydatas.type" :label="1">文字图片底部</el-radio>
                                <el-radio style="margin-left:0px; margin-right:10px;"  v-model="mydatas.type" :label="2">文字图片居中</el-radio>

                        </el-form-item>  

                        <el-form-item label="标题字号">
                             <el-slider  :min="12" :max="50"  v-model="mydatas.titleSize"></el-slider>
                        </el-form-item>
                         <el-form-item label="标题加粗">    
                                <el-radio style="margin-left:0px; margin-right:10px;" v-model="mydatas.isB" :label="0">默认</el-radio>
                                <el-radio style="margin-left:0px; margin-right:10px;"  v-model="mydatas.isB" :label="1">加粗</el-radio>
                        </el-form-item> 
                         <el-form-item label="对齐方式">    
                                <el-radio style="margin-left:0px; margin-right:10px;" v-model="mydatas.titleType" :label="0">左对齐</el-radio>
                                <el-radio style="margin-left:0px; margin-right:10px;"  v-model="mydatas.titleType" :label="1">居中对齐</el-radio>
                                <el-radio style="margin-left:0px; margin-right:10px;"  v-model="mydatas.titleType" :label="2">右对齐</el-radio>
                        </el-form-item>             
                        <el-form-item   label="文字颜色">  
                            <el-color-picker  v-model="mydatas.titleColor"  size="mini"></el-color-picker>
                        </el-form-item>
                        <el-form-item   label="文字背景">  
                            <el-color-picker  v-model="mydatas.titleBg"  size="mini" show-alpha></el-color-picker>
                        </el-form-item>  
                         <el-form-item label="标题左间距">
                             <el-slider  :min="0" :max="40"  v-model="mydatas.titleL"></el-slider>
                        </el-form-item>
                         <el-form-item label="文字容器高度">
                             <el-slider   v-model="mydatas.titleH"></el-slider>
                        </el-form-item>
   
                  



                    </el-form>

                
                   
                </el-tab-pane>
                <el-tab-pane label="基本设置" name="second">                      
                     <edit-com :datas="mydatas" v-model="mydatas"></edit-com>
                </el-tab-pane>
               
            </el-tabs>
         
      </div>
  </div>
</template>

<script>
import editCom from '../EditCom.vue';
import SelectLink from  '../SelectLink';
import selectPhoto from '../SelectPhoto';
export default {
  name: 'pluginTuwen',
  props: ['type', 'datas'],
  components:{
      editCom,
      SelectLink,
      selectPhoto
  },
  data(){
      return {
          activeName:'first',
          mydatas:[],
      };
  },
  watch:{
      datas:{
           handler(newName, oldName) {
                if(this.datas != this.mydatas){
                    this.mydatas = this.datas;
                }
            },
            immediate: true,
            deep: true
      },
      mydatas:{
           handler(newName, oldName) {
                if(this.datas != this.mydatas){
                    this.$emit('input',this.mydatas);
                }
            },
            immediate: true,
            deep: true
      }
  },
  methods: {
      getStyle(){
           let style = new Object;
           
          
           return style;
      },
      getStyleByItem(item){
          let style = new Object;
          style.height = this.mydatas.titleH+'px';
          if(this.mydatas.type ==0){
              style.paddingLeft = this.mydatas.titleL + 'px';
              style.width = '100%';
          }else{
              style.position = 'absolute';
              style.width = 'calc(100% - '+this.mydatas.titleL * 2+'px)'; 
              style.left = this.mydatas.titleL + 'px';
              if(this.mydatas.type == 1){ //距离底部的
                 style.bottom = '0px';
              }else{//距离上面的
                 style.top = (this.mydatas.height -this.mydatas.titleH )/2 +'px';   
              }
          }

          style.background = this.mydatas.titleBg;
          style.color = this.mydatas.titleColor;
          style.lineHeight =   this.mydatas.titleH+'px';
          if(this.mydatas.titleType == 0){
              style.textAlign = 'left';
          }else if (this.mydatas.titleType == 1){
              style.textAlign = 'center';
          }
          else{
              style.textAlign = 'right';
          }  
          style.fontWeight = this.mydatas.isB ? 'bold' : '100';
          style.fontSize = this.mydatas.titleSize + 'px';

          return style;  
      },
   
  },
}
</script>
<style scoped>
  .plugin-tuwen-item{
      position: relative;
      width: 100%;
  }
</style>