<template>
  <div class="select-link">
      <a @click="dialogVisible = true"  href="javascript:void(0);">选择链接</a>

      
    <el-dialog
        title="选择链接"
        :visible.sync="dialogVisible"
        :modal-append-to-body="false"
        width="800px"
       >

       <el-form  size="mini" label-width="100px">
          <el-form-item label="选择链接">
              <el-select size="small" v-model="selectId" placeholder="请选择">
                <el-option
                  v-for="(item,key) in linkdata"
                  :key="key"
                  :label="item.name"
                  :value="key">
                </el-option>
              </el-select>
          </el-form-item>

          <el-form-item  v-for="(item,index) in selectData" :key="index" :label="item.name">

             <el-input  v-if="item.type==1" size="mini" style="width:200px;" v-model="selectData[index].val" placeholder="请填写"></el-input>

             <el-select v-if="item.type==2" size="small" v-model.number="selectData[index].val" placeholder="请选择">
                <el-option
                  v-for="(it2,k2) in item.datas"
                  :key="k2"
                  :label="it2.name"
                  :value="it2.val">
                </el-option>
              </el-select> 

          </el-form-item>

          <el-form-item>
              <el-button size="small" class="el-button" @click="dialogVisible = false">取 消</el-button>
              <el-button  size="small" class="el-button"  type="primary" @click="yes">确 定</el-button>
          </el-form-item>
       </el-form>
        
      
       </el-dialog>  

  </div>
</template>

<script>
import * as  style  from '../style.js';
//电话是wyu_tel  跳转小程序是 wyu_tominiapp;

export default {
  name: 'selectLink',
  props:[],
  data(){
      return {
        dialogVisible:false,
        linkdata: JSON.parse(JSON.stringify(style.linkdata)),
        selectId: 0,
        selectData:[],//选的数据
      };
  },
  watch:{
    selectId:function(val){
      for(var a in this.linkdata){
        if(a == val){
            this.selectData = JSON.parse(JSON.stringify(this.linkdata[a].datas));
            break;
        }
      }
    }
  },
  methods:{
      yes(){
          let link  = this.linkdata[this.selectId].page ? this.linkdata[this.selectId].page : null;
          if(!link) return;
          if(link.indexOf('?')>-1){
              if(this.selectData !=''){
                link += '&';
              }
          }else{
              if(this.selectData !=''){
                    link += '?';
              }
          }
          for(var a in this.selectData){
              link += this.selectData[a].key+'='+this.selectData[a].val;
              if(a+1 < this.selectData.length){
                  link+='&';
              }
          }
          this.$emit('input', link);
          this.dialogVisible = false;
      },
  },
}
</script>

<style scoped>
.select-link{
  float: left;
  margin-left: 10px;
}
.select-link a{
  color:#999999;
}
</style>
