<template>
  <div>
      <div v-if="type == 'show'"  :style="getStyle()">
            <img :style="'width:100%;height:'+mydatas.height+'px;'" :src="mydatas.pic ? mydatas.pic : '/miniapp/shop/img/img.png'" />
      </div>
      <div v-if="type  == 'edit'" >
             <el-tabs v-model="activeName">
                <el-tab-pane label="内容设置" name="first">
                    <el-form style="padding:10px;" size="mini" label-width="80px">
                        <el-form-item label="上传图片">
                                <wyu-upload  v-model="mydatas.pic" :img="mydatas.pic"></wyu-upload>
                        </el-form-item>
                        <el-form-item label="图片高度">    
                               <el-input-number v-model="mydatas.height"  :min="0" :max="1000" label="图片高度"></el-input-number>
                        </el-form-item>     
                        <el-form-item label="图片链接">    
                               <el-input placeholder="选择链接" v-model="mydatas.link"  class="input-select-link"></el-input>
                               <select-link v-model="mydatas.link"></select-link>
                        </el-form-item>          

                    </el-form>  
                </el-tab-pane>
                <el-tab-pane label="基本设置" name="second">                      
                     <edit-com :datas="mydatas" v-model="mydatas"></edit-com>
                </el-tab-pane>
               
            </el-tabs>
         
      </div>
  </div>
</template>

<script>
import WyuUpload from "@/components/Photos";
import SelectLink from  '../SelectLink';

import editCom from '../EditCom.vue';
export default {
  name: 'pluginPhoto',
  props: ['type', 'datas'],
  components:{
      editCom,
      WyuUpload,
      SelectLink
  },
  data(){
      return {
          activeName:'first',
          mydatas:[],
      };
  },
  watch:{
      datas:{
           handler(newName, oldName) {
                if(this.datas != this.mydatas){
                    this.mydatas = this.datas;
                }
            },
            immediate: true,
            deep: true
      },
      mydatas:{
           handler(newName, oldName) {
                if(this.datas != this.mydatas){
                    this.$emit('input',this.mydatas);
                }
            },
            immediate: true,
            deep: true
      }
  },
  methods: {
      getStyle(){
           let style = new Object;
           return style;
      },
  },
}
</script>
